<template>
<div class="loader">
  <svg class="circular" viewBox="10 10 20 20">
    <circle class="path" cx="20" cy="20" r="5" fill="none" stroke-width="2" stroke-miterlimit="10"/>
  </svg>
</div>
</template>

<script>
export default {
  data () {
    return {}
  }
}
</script>

<style lang='scss' scoped>
  .loader {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: var(--white);
  }
  
  .path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round;
  }
  
  .circular {
  animation: rotate 2s linear infinite;  
  transform-origin: center center;
  height: 100px;
  width: 100px;
  position: relative;
  top: 200px; 
  left: 35%;  
  margin: auto;
  }
@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes dash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35px;
    }
    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124px;
    }
}

@keyframes color {
    100%,
    0% {
        stroke: #f0807e;
    }
    40% {
        stroke: #f5bea9;
    }
    66% {
        stroke: #b7db81;
    }
    80%,
    90% {
        stroke: #81ac71;
    }
}

</style>



