let trickCates = {
  // 個股趨勢
  stock: {
    name: '個股趨勢'
  },
  // 關鍵排行
  rank: {
    name: '關鍵排行'
  },
  // 市場脈動
  market: {
    name: '市場脈動'
  }
}

export default trickCates

export function excludeTrickCates (cate) {
  let subCate = cate.split('-')[1]

  // TODO 改 no in
  return subCate !== 'K線'
}



