<template>
<input v-model="value" @change="changeValue" />
</template>
<script>
export default {
  props: {
    param: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      value: this.param.val
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>



