<template>
  <div class="index">
    <h1>TODO 首頁，目前是空的。</h1>

    <!-- <ul class="list-group" v-sortable>
         <li class="list-group-item">Foo</li>
         <li class="list-group-item">Bar</li>
         <li class="list-group-item">Baz</li>
         </ul> -->

    <!-- <ul v-sortable:basic class="list-group">
         <li class="list-group-item" v-for="item in list">{{ item }}</li>
         </ul> -->
    <ul v-sortable:basic class="list-group">
      <draggable v-model="list">
        <li class="list-group-item" v-for="item in list">{{ item }}</li>
      </draggable>
    </ul>

  </div>
</template>

<script>

import draggable from 'vuedraggable'

export default {
  /* props: {
   *   $StockCodeSearchHistory: {
   *     type: Array
   *   }
   * }, */
  components: {
    draggable
  },
  data () {
    return {
      list: ['Foo', 'Bar', 'Baz']
    }
  },
  methods: {
    onUpdate: function (event) {
      this.list.splice(event.newIndex, 0, this.list.splice(event.oldIndex, 1)[0])
    }
  },
  watch: {
    list (val) {
      console.log(val)
    }
  }
}
</script>

<style lang="scss" scoped>
  .index {
    height: 600px;
    width: 100%;
  }

  // test
  .list-group {
    padding-left: 0;
    margin-bottom: 20px;

    .list-group-item {
      position: relative;

      display: block;

      padding: 10px 15px;

      margin-bottom: -1px;

      background-color: var(--white);

      border: 1px solid #ddd;
    }
  }
</style>



