<template>
  <div class="inputCmp">
    <div class="input-search">
      <input
            v-model="value"
            value="value"
            @change="changeValue"
            @focus="fetchAC"
            @keyup="fetchAC"
            @blur="inputBlur"
            :placeholder='placeholder' />
      <ul v-show="showACDropdown" class="ac-dropdown">
        <li class="ac-candidates" @mousedown="chooseAC(match.code)" v-for="match in acMatches">
          {{match.name + match.code}}
        </li>
      </ul>
    </div>

    <div class="icon-search">
      <img src="../assets/img/new/search.png" alt="search"/>
    </div>
  </div>
</template>

<script>
import multiac from '@/lib/multiac'
import _debounce from 'lodash.debounce'

export default {
  created () {
    this.fetchSettle()
    window.addEventListener('keydown', this.ulKeyUpHandler)
  },
  props: {
    sym: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '輸入個股關鍵字'
    },
    acfn: {
      type: Function,
      default: (kw) => multiac('tw', 'twstk', kw)
    }
  },
  data () {
    return {
      value: this.sym,
      valueIsOnFocus: false,
      acMatches: [],
      index: -1,

    }
  },
  computed: {
    showACDropdown: function () {
      if (this.acMatches.length > 0 && this.valueIsOnFocus === true) { return true } else { return false }
    }
  },
  watch: {
    acMatches: () => {
      if (this === []) this.showACDropdown = false
    }
  },
  methods: {
    changeValue () {
      this.$emit('changeValue', this.value)
    },
    fetchAC: _debounce(function (e) {
      this.valueIsOnFocus = true
      let keyCode = e.keyCode
      let nums = [38, 40, 13]
      this.value = this.value.replace(/[!@#$%^&*+_]/g, '')
      if (this.value.trim() === '' || nums.indexOf(keyCode) != -1) return
      let data = this.acfn(this.value)

      this.acMatches = []
      this.index = -1
      data.then(rlt => {
        rlt.forEach(element => {
          this.acMatches.push(element)
        })
      })
    }),

    chooseAC (match) {
      this.value = match
      this.fetchSettle()
      this.changeValue()
    },
    fetchSettle () {
      this.$emit('fut', this.value)
    },
    ulKeyUpHandler (e) {
      let keyCode = e.keyCode
      if (keyCode === 13) this.acMatches = []

      let nums = [38, 40] // 處理上下按鈕
      if (this.valueIsOnFocus != true || nums.indexOf(keyCode) === -1) return
      keyCode === 38 ? this.index-- : this.index++
      if (this.index === -2) this.index = -1

      let allLi = document.querySelectorAll('.ac-candidates')
      if (this.index === -1 || this.index === -2) this.index = allLi.length - 1
      if (this.index === allLi.length) this.index = 0

      allLi.forEach(item => { item.classList.remove('acItem') })
      let activeLi = document.querySelectorAll('.ac-candidates')[`${this.index}`]
      activeLi.classList.add('acItem')
      this.value = this.acMatches[`${this.index}`].code
    },
    inputBlur () {
      this.acMatches = []
      this.index = -1
    }
  },
  beforeDestroy () {
    window.removeEventListener('keydown', this.ulKeyUpHandler)
  }
}
</script>
<style lang="scss" scoped>
  .inputCmp {
    // position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .input-search{
      > input {
        padding: 6px 0px 5px 10px;
        border-radius: 20px;
        border-color: transparent;
        box-shadow: 0 2px 4px 0 var(--dark-grey-blue-20);
        border-bottom-right-radius: unset;
        border-top-right-radius: unset;
        &:focus{
          border-bottom-left-radius: unset;
        }
      }
      .ac-dropdown {
        z-index: 2;
        margin: 0;
        padding: 0;
        width: 140px;
        list-style-type: none;
        background-color: var(--white);
        position: absolute;
        border-bottom: 1px solid var(--dark-grey-blue-20);
        border-bottom-right-radius: 15px;
        border-bottom-left-radius: 15px;
        box-shadow: 1px 1px 3px -1px var(--trickbox-text);
        > li {
          cursor: pointer;
          padding: 2px 10px;
          white-space: nowrap;
          border: 1px solid var(--dark-grey-blue-20);
          border-bottom: none;
        }
        > li:hover {
          background-color: rgba(25, 137, 172, 0.05);
        }

        li:last-child{
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
    }

    .icon-search{
      display: flex;
      padding: 5px 15px 5px 10px;
      box-shadow: 0 2px 4px 0 rgba(40, 62, 86, 0.3);
      background-image: linear-gradient(to right, var(--sea-turtle), #19ac8d);
      border-radius: 20px;
      border-bottom-left-radius: unset;
      border-top-left-radius: unset;
    }
  }
</style>



