function parseTrickParam (trick) {
  let headerStart = trick.indexOf("[")
  let inputStart = trick.indexOf("(")
  if (headerStart === -1 && inputStart === -1)
    return [trick, undefined, undefined]

  let headers = undefined
  let inputs = undefined
  let trickName = ''

  if (headerStart !== -1) {
    headers = trick.substring(headerStart + 1, trick.indexOf(']'))
      .split(';')

    trickName = trick.substring(0, headerStart)
  }

  if (inputStart !== -1) {
    inputs = trick.substring(inputStart + 1, trick.indexOf(')'))
      .split(';')
    if (headerStart === -1) {
      trickName = trick.substring(0, inputStart)
    }
  }

  return [trickName, headers, inputs]
}

export function parseUrl (trickUrl) {
  if (trickUrl === '') return {}
  let tricks = trickUrl.split(',')
      .map(parseTrickParam)
      .reduce((acc, it) => {
        acc[it[0]] = {headers: it[1], inputs: it[2]}
        return acc
      }, {})

  return tricks
}

export function genUrl (tricks) {

  /**
   * TODO rename
   * @param {} it
   * @returns {}
   */
  function mgfn (it) {
    return (it +
            (tricks[it].headers === undefined
             ? ''
             : "[" + tricks[it].headers.join(";") + "]") +
            ((tricks[it].inputs === undefined || tricks[it].inputs.length === 0)
             ? ''
             : "(" + tricks[it].inputs.join(";") + ")"))
  }

  return Object.keys(tricks)
    .map(mgfn)
    .join(",")
}

export function getUrlHeaders (urlParam, trickname) {
  let tricks = parseUrl(urlParam)
  if (tricks === undefined || tricks[trickname] === undefined) {
    return undefined
  }
  return tricks[trickname].headers
}



