/**
 * @fileOverview
 * @name trickapi
 * @author zx
 * TODO api 說明
 */
import axios from 'axios'

const domain = process.env.TRICK_URL

/**
 * 把 trick 分類中的每一個 item 修正為 table 的網址
 * @param {Object} cate - Trick 分類
 * @returns {Object}
 */
function translateItems (cate) {
  return ({
    name: cate.name,
    items: cate.items.map(it => ({ ...it,
      href: it.href.substr(1).replace(/\//g, '_')
    }))
  })
}

/**
 * @returns {}
 */
async function getJson () {
  let res = await axios(domain + '/tricksJson')
  return res.data
}

/**
 * @returns {}
 */
export async function trickapi () {
  let trickJson = await getJson()
  let data = {'cates': trickJson.cates.map(cate => translateItems(cate))} // return 函示需要 'cates'

  return ({
    getCates: function () {
      return data.cates.map(el => el.name)
    },
    getCateItems: function (cateStr) {
      let cate = data.cates.find(el => el.name === cateStr)
      return cate && cate.items
    },
    getCateItemURL (cateStr, trick) {
      // TODO 改成 hash map
      let cate = data.cates.find(el => el.name === cateStr)
      let item = cate && cate.items.find(it => it.desc === trick)
      let urlParam = item && '?' + item.params.map(param => param.var + '=' + param.val).join('&')
      return cate && item && item.href + urlParam
    },
    getCateItemInputs (cateStr, trick) {
      let cate = data.cates.find(el => el.name === cateStr)
      let item = cate && cate.items.find(it => it.desc === trick)
      return cate && item && item.params
    }
  })
}

// TODO 改名 fetch
export async function getTable (path, inputs, shows, sorts) {
  let show = shows ? '&show=' + shows.join(';') : ''
  let sort = sorts && sorts.length !== 0 ? '&s=' + sorts.map(it => it.f + ':' + it.s).join(';') : ''
  // console.log(domain + path + '?' + inputs + show + sort)
  let res = await axios(domain + path + '?' + inputs + show + sort)
  return res
}

export async function getTVarJson (tvar, params) {
  let target = 'TVarJson_' + tvar
  let param = params === undefined ? '' : '?' + params.join('&')
  let res = await axios(domain + '/json/' + target + param)
  return res
}

export function getSymCname (sym, fn) {
  // NOTE 這邊因為要給沒辦法 async 的地方用，所以只好包 callback
  let url = domain + '/symName' + '?sym=' + sym
  axios(url).then(res => {
    if (fn) {
      fn(res.data)
    }
  })
}

/**
 * TODO
 * @param {} page
 * @returns {}
 */
export async function getDefaultTrick (page) {
  let url = domain + '/defaultTrick' + '?page=' + page
  let res = await axios(url)
  return res.data
}



