<template>
  <div>
  </div>
</template>

<script>
  /*
   * @vue-prop {Object} options - Highcharts chart configuration object
   * @vue-prop {String} constructor-type - Chart constructor type using to init specific chart. Allowed options: 'chart', 'stockChart', 'mapChart'. First one is set for default.
   * @vue-data {Function} callback - Function passed as a callback during chart init, and triggered when chart is loaded.
   * @vue-computed {Array} updateArgs - Array of update()'s function optional arguments. Parameters should be defined in the same order like in native Highcharts function: [redraw, oneToOne, animation]. Here is a more specific description of the parameters: https://api.highcharts.com/class-reference/Highcharts.Chart#update
   */

  import Highcharts from 'highcharts/highstock'
  import HighchartsMore from 'highcharts/highcharts-more'

  import darkunica from './theme.js' // TODO generalize

  Highcharts.setOptions({
    lang:{
      rangeSelectorZoom: '',
      thousandsSep: ','
    }
  })

  HighchartsMore(Highcharts)

  export default {
    props: {
      options: {
        type: Object,
        default: function() {
          return {}
        }
      },
      theme: {
        type: Object,
        default: function() {
          return {}
        }
      },
      // 形態待確認
      Highcharts: {
        default: ''
      },
      // 形態待確認
      chart: {
        default: ''
      }
    },
    // ['options', 'Highcharts', 'chart', 'theme'],
    name: 'VueHighcharts',
    data () {
      return {
        _chart: null
      }
    },
    mounted() {
      if (!this.getChart() && this.options) {
        this.init();
      }
    },

    methods: {
      getChart(){
        return this._chart
      },
      addSeries(options){
        this.delegateMethod('addSeries', options);
      },
      removeSeries(){
        while(this.getChart().series.length !== 0) {
          this.getChart().series[0].remove();
        }
      },
      mergeOption(options){
        this.delegateMethod('update', options)
      },
      showLoading(txt){
        this.getChart().showLoading(txt);
      },
      hideLoading(){
        this.getChart().hideLoading();
      },
      delegateMethod(name, ...args){
        if (!this.getChart()) {
          //warn(`Cannot call [$name] before the chart is initialized. Set prop [options] first.`, this)
          return
        }
        return this.getChart()[name](...args)
      },

      init(){
        if (!this.getChart() && this.options) {
          let _Highcharts = this.Highcharts || Highcharts;

          // TODO check theme parameters
          // if theme == darunico ...

          _Highcharts.theme = this.theme
          _Highcharts.setOptions(_Highcharts.theme)

          if (_Highcharts.product == 'Highstock') {
            this._chart = new _Highcharts.stockChart(this.$el, this.options);
          } else {
            this._chart = new _Highcharts.Chart(this.$el, this.options);
          }
        }
        this.$emit('updated', this._chart)
      },
      destroyElement(){
        // Destroy method
        this.$destroy();
      }
    },

    watch: {
      options: function (options) {
        if (!this.getChart() && options) {
          this.init()
        } else {
          this.getChart().update(this.options);
        }
      },
      theme: function (v) {
        console.log(v)
      }
    },

    beforeDestroy(){
      if (this.getChart()) {
        this.getChart().destroy();
      }
    }
  }
</script>

<style lang="scss">
</style>



