<template>
  <div class="trick-box"
    :id="'trick-box-' + trick.ename"
    :trick="trick.ename">
    <div class="trick-box-header">
      <div class="trick-name">{{trick.desc}}</div>
      <div class="option">
        <div class="zoom" @click="zoom">
          <img src="../../assets/img/search-plus.svg" alt="zoom" />
        </div>
      </div>
    </div>
    <TrickInputBar
      :params="params"
      @update='handleUpdateParam' />
    <!-- 判斷是 trick 還是其它 table -->
    <div v-if="trick['component']" >
      <!-- <h1>{{trick['component']}}</h1> -->
      <component :is="trick['component']" :code="sym" :isZoomed="isZoomed"/>
    </div>
    <div v-else class='trick-table'>
      <TrickTable
        :desc="trick.desc"
        :ename="trick.ename"
        :input="input"
        :href="href"
        :isZoomed="isZoomed"
        @headerChange="handleHeaderChange" />
    </div>
    <div v-if="!trick['component']"  class="trick-box-footer">
      <div class="download">
        <img src="../../assets/img/download.svg" alt="download" />
        <a :href="pdfUrl" target="_blank">PDF</a>
        <a :href="excelUrl" target="_blank">EXCEL</a>
        <a :href="jsonUrl" target="_blank">JSON</a>
      </div>
      <div class="page-control">
        <p @click="pervPage(10)">顯示上10筆</p>
        <p @click="nextPage(10)">顯示下10筆</p>
      </div>
    </div>
  </div>
</template>

<script>
import TrickTable from './TrickTable.vue'
import TrickInputBar from './TrickInputBar.vue'
import CandleStick from '@/components/CandleStick'
import StockNews from '@/components/StockNews'

import {parseUrl, genUrl} from '@/lib/url'

const domain = process.env.TRICK_URL

export default {
  components: {
    TrickTable, TrickInputBar, CandleStick, StockNews
  },
  props: {
    trick: {
      default: {}
    },
    // 在 trick.desc 內就有了
    /* desc: {
     *   default: ''
     * }, */

    /**
     * 只有個股會用到，個股的 trick 會指定 sym 參數
     */
    sym: {
      default: undefined
    }
  },
  data () {
    return {
      params: [],
      input: '',
      href: '',
      isZoomed: false,
      showDownloadTooltip: false,
      pdfUrl: '',
      excelUrl: '',
      jsonUrl: '',
      //    起始 結束筆數
      psize: 10,
      p: 1,
      macroInput: ''
    }
  },
  created () {
    let tricks = parseUrl(this.$route.query.trick)
    let currTrick = tricks[this.trick.ename]

    let inputSource

    if (currTrick === undefined || currTrick.inputs === undefined) {
      inputSource = this.trick['params']
    } else {
      inputSource = currTrick.inputs.map(it => {
        let input =  it.split('=')
        return ({var: input[0], val: input[1]})
      })
    }

    if (this.sym !== undefined) {
      // NOTE 外部選定 sym 參數
      this.params = inputSource.filter(it => it['var'] !== 'sym')
    } else {
      this.params = inputSource
    }

    this.input = this.params.map(it => it.var + '=' + it.val).join('&')
               + (this.sym !== undefined ? `&sym=${this.sym}` : '')
               + `&psize=${this.psize}&p=${this.p}`

    this.href = `/html/${this.trick['href']}`
    this.pdfUrl = `${domain}/pdf/${this.trick['href']}?${this.input}`
    this.excelUrl = `${domain}/excel/${this.trick['href']}?${this.input}`
    this.jsonUrl = `${domain}/json/${this.trick['href']}?${this.input}`
  },
  methods: {
    zoom () {
      if (!this.isZoomed) {
        document.getElementById(`trick-box-${this.trick.ename}`).style.flex = '0 1 100%'
      } else {
        document.getElementById(`trick-box-${this.trick.ename}`).style.flex = '0 1 calc((100% - 20px)/2)'
      }
      this.isZoomed = !this.isZoomed
    },
    toggleTooltip () {
      this.showDownloadTooltip = !this.showDownloadTooltip
    },
    pervPage (v) {
      if (this.p - 1 === 0) {
        this.p -= 1
      }
      this.p -= 1
      this.updateInput()
    },
    nextPage (v) {
      if (this.p + 1 === 0) {
        this.p += 1
      }
      this.p += 1
      this.updateInput()
    },
    updateInput() {
      // TODO 個股的和一的不同
      /* let path = this.$route['path'].split('/')
       * let sym = path[2] */
      if (this.sym !== undefined) {
        this.input = `${this.macroInput}&sym=${this.sym}&psize=${this.psize}&p=${this.p}`
      } else {
        this.input = `${this.macroInput}&psize=${this.psize}&p=${this.p}`
      }
    },
    handleUpdateParam (newParam) {
      // todo 把 input 加入 url ( 有變更的 )
      if (newParam !== undefined || newParam.length === 0) {
        this.macroInput = newParam.join('&')
      }
      /* this.$router.replace({query: {trick: newquerytrick}}) */
      // 更新 input

      let tricks = parseUrl(this.$route.query.trick)
      let currTrick = tricks[this.trick.ename]
      if (currTrick !== undefined) {
        currTrick.inputs = newParam
      }
      let newQueryTrick = genUrl(tricks)
      this.$router.replace({query: {trick: newQueryTrick}})

      this.updateInput()
    },
    handleHeaderChange (trickName, newHeader) {
      // TODO trickName 不需要
      let tricks = parseUrl(this.$route.query.trick)
      let currTrick = tricks[trickName]
      if (currTrick !== undefined) {
        currTrick.headers = newHeader
      }
      let newQueryTrick = genUrl(tricks)
      this.$router.replace({query: {trick: newQueryTrick}})
    }
  },
  watch: {
    sym (val) {
      // NOTE 只有傳 sym 的才會 trigger
      if (this.sym !== undefined && this.sym.length >= 4) {
        // NOTE 外部選定 sym 參數
        this.params = this.trick['params'].filter(it => it['var'] !== 'sym')
        this.input = this.params.map(it => it.var + '=' + it.val).join('&')
        this.input += "&sym=" + this.sym
      }
    }
  }
}
</script>

<style lang='scss' scoped>
  * {
    box-sizing: border-box;
  }
  .trick-box {
    display: flex;
    flex-direction: column;
    min-width: 400px;
    flex: 0 1 calc((100% - 20px)/2);
    border-radius: 6px;
    transition: all 1s;
    height: 600px;
    margin: 0 5px 20px 5px;
    background-color: var(--tab-bg-color);
    border-bottom: 2px solid var(--sea-turtle);
    border-radius: 4px;

    .trick-table {
      background-color: var(--white);
      flex: 1 1 200px;
      overflow: auto;
    }
    > .trick-box-footer {
      display: flex;
      justify-content: space-between;
      > .download {
        position: relative;
        display: flex;
        padding: 10px;
        > img {
          margin: 0 10px;
          width: 20px;
        }
        > a {
          text-decoration: none;
          font-family: Microsoft JhengHei;
          font-weight: 300;
          font-size: 14px;
          letter-spacing: 1px;
          color: var(--trickbox-text);
          margin: auto 5px;
        }
      }
      > .page-control {
        display: flex;
        > p {
          margin: auto 5px;
          font-family: MicrosoftJhengHei;
          font-size: 12px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: 1.2px;
          color: var(--sea-turtle);
          white-space: nowrap;
          cursor: pointer;
        }
      }
    }
  }
  .trick-box-header {
    display: flex;
    justify-content: space-between;
    flex: 0 1 auto;
    width: 100%;
    padding: 5px 15px;
    > * {
      display: inline-block;
    }
    .trick-name {
      font-family: MicrosoftJhengHei;
      font-weight: 500;
      color: var(--sea-turtle);
      font-size: 18px;
    }
    .option {
      > * {
        display: inline-block;
      }
      img {
        width: 20px;
        height: 20px;
        cursor: pointer;
        vertical-align: bottom;
      }
      > .zoom {
        > .icon {
          font-size: 50px;
        }
      }
    }
  }
</style>



