<template>
    <div class="inputCmp">
        <input
            v-model="value"
            value="value"
            @change="changeValue"
            @focus="fetchAC"
            @keyup="fetchAC"
            @blur="inputBlur"
            placeholder='輸入欲查詢的關鍵字' />
        <ul v-show="showACDropdown" class="ac-dropdown">
            <li class="ac-candidates" @mousedown="chooseAC(sect.code)" v-for="sect in sects">
                {{sect.name}}
            </li>
        </ul>
    </div>
</template>
<script>
import fetchAC from '@/lib/multiac'

export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    return {
      value: this.param.val,
      valueIsOnFocus: false,
      sects: [],
      index: -1
    }
  },
  created () {
    window.addEventListener('keydown', this.ulKeyUpHandler)
  },
  computed: {
    showACDropdown: function () {
      if (this.sects.length > 0 && this.valueIsOnFocus === true) { return true } else { return false }
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    },
    fetchAC (e) {
      this.valueIsOnFocus = true
      let keyCode = e.keyCode
      let nums = [38, 40, 13]
      this.value = this.value.replace(/[^a-zA-Z0-9]/g, '')
      if (this.value.trim() === '' || nums.indexOf(keyCode) != -1) return
      let data = fetchAC('tw', 'TdrDivSum', this.value)

      this.sects = []
      this.index = -1
      data.then(rlt => {
        rlt.forEach(element => {
          this.sects.push(element)
        })
      })
    },
    chooseAC (match) {
      this.value = match
      this.changeValue()
    },
    async getJSON (url, param) {
      let result = querystring.stringify(param)

      let d = await axios.get('http://trick.prj.tw/json/TVarJson_ii3Institute')
      this.sects = d.data.data
    },
    ulKeyUpHandler (e) {
      let keyCode = e.keyCode
      if (keyCode === 13) this.sects = []

      let nums = [38, 40] // 處理上下按鈕
      if (this.valueIsOnFocus != true || nums.indexOf(keyCode) === -1) return
      keyCode === 38 ? this.index-- : this.index++

      let allLi = document.querySelectorAll('.ac-candidates')
      if (this.index === -1 || this.index === -2) this.index = allLi.length - 1
      if (this.index === allLi.length) this.index = 0

      allLi.forEach(item => { item.classList.remove('acItem') })
      let activeLi = document.querySelectorAll('.ac-candidates')[`${this.index}`]
      activeLi.classList.add('acItem')
      this.value = this.sects[`${this.index}`].code
    },
    inputBlur () {
      this.valueIsOnFocus = false
    }
  },
  beforeDestroy () {
    window.removeEventListener('keydown', this.ulKeyUpHandler)
  }
}
</script>



