<template>
<select v-model="value" @change="updateValue">
  <option value="Cement">Cement</option>
  <option value="Food">Food</option>
  <option value="Tour">Tour</option>
  <option value="電子商務">電子商務</option>
</select> 
</template>
<script>
export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    return {
      value: this.param.val
    }
  },
  methods: {
    updateValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>



