<template>
  <div class='main-body'>
    <div class="side-board">
      <SideBoard
        :options="sideBoardOptions"
        :searchPlaceHolder="'輸入欲查詢的券商'"
        @option-change="handleOptionChange"
        @target-search="handleTargetSearch" />
    </div>
    <div class='box-container'>
      <div class="broker-box-grid">
        <BrokerBox :key="key" :brokerName="key" :reports="reports"
          @open-pdf="openPdf"
          v-for="(reports, key) in resultList"></BrokerBox>
      </div>
    </div>
    <div v-if="openIFrame" class="report-view">
      <button @click="closeIFrame">X</button>
      <iframe :src="iframeLink"></iframe>
    </div>
  </div>
</template>

<script>
// import TargetSearchBar from '@/components/TargetSearchBar.vue'
import SideBoard from '@/components/SideBoard'
import HKReport from '@/components/HKReport.vue'
import BrokerBox from '@/components/BrokerBox.vue'
import multiac from '@/lib/multiac'
import * as R from 'ramda'

import reportapi from '../lib/reportapi'
import reportOptions from '../defines/HKReport'
import { date } from 'js-common'

export default {
  components: {
    SideBoard, BrokerBox, HKReport
  },
  data () {
    return {
      // right_options: reportOptions.type, // Note 只用到 left options
      // left_options: reportOptions.exchange, // Note 只用到 left options
      sideBoardOptions: reportOptions,
      selectReportType: reportOptions.default,
      resultList: {},
      reports: {},
      iframeLink: '',
      openIFrame: false,
      init: false, // 控制 Loading 畫面的
      // mode: 'stk',
      // brokers: null
    }
  },
  created () {
    this.getReports()
  },
  methods: {
    //async handleAC (keyword) {
    //  // 港股券商 ac
    //  let k = keyword.split(/#/)
    //  return await multiac('tw', 'hk', k[0])
    //},
    //async handleStkAC (keyword) {
    //  return await multiac('tw', 'hkreport', keyword)
    //},
    openPdf (path) {
      this.openIFrame = true
      this.iframeLink = 'http://report.above.tw' + path
    },
    closeIFrame () {
      this.openIFrame = false
    },
    // for dev
    getRandomIntInclusive (min, max) {
      min = Math.ceil(min)
      max = Math.floor(max)
      return Math.floor(Math.random() * (max - min + 1)) + min
    },
    getReports: async function () {
      let report = await reportapi()
      let cates = report.cates.map(it => it.category)
      this.reports = report.cates.reduce((acc, it) => (acc[it.category] = it.pdfs) ? acc : acc, {})
      this.updateReports()
    },
    updateReports () {
      let prevMonth = (new Date()).setMonth((new Date()).getMonth() - 4) // TODO 對定是要幾個月之前
      if (this.selectReportType === 'all') {
        let allReport = R.flatten(Object.keys(this.reports).map(type => this.reports[type]))
        let currReports = R.filter(it => new Date(it.date) > prevMonth)(allReport)
        this.resultList = R.groupBy((it) => it.issue, currReports)
      } else {
        let currReports = R.filter(it => new Date(it.date) > prevMonth)(this.reports[this.selectReportType])
        this.resultList = R.groupBy((it) => it.issue, currReports)
      }
    },
    reportGroupByDate (report) {
      // NOTE 依日期分類的 function
      return date.getYM(report.date, "yyyy/MM/dd")
    },
    handleTargetSearch (newSym) {
      // TODO 更新 this.resultList
      if (this.selectReportType === 'all') {
        let allReport = R.flatten(Object.keys(this.reports).map(type => this.reports[type]))
        let currReports = R.filter(it => it.sym === newSym)(allReport)
        this.resultList = R.groupBy((it) => it.issue, currReports)
      } else {
        let currReports = R.filter(it => it.sym === newSym)(this.reports[this.selectReportType])
        this.resultList = R.groupBy((it) => it.issue, currReports)
      }
    },
    handleOptionChange (val) {
      this.selectReportType = val
      this.updateReports()
    }
  }
}
</script>

<style lang='scss' scoped>
  .report-view {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 1080px;
    background-color: grey;
    iframe {
      width: 100%;
      height: 1080px;
    }
  }
  .broker-box-grid {
    display: flex;
    flex-grow: 3;
    flex-wrap: wrap;
  }
  .main-body {
    min-height: 70vh;
    height: auto !important;
    width: 100%;

    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    flex-direction: row;
  }
  .side-board {
    width: 20%;
    background-color: #fff;
    min-height: 800px;
  }

  .box-container {
    width: 70%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: row;
    padding: 0 20px;
  }

</style>



