<template>
  <div>
    <select v-model="value" @change="changeValue" v-html="ii3InstituteOption">
      <!-- <template v-for="sect in sects">
        <option :value="sect">{{sect}}</option>
      </template> -->
    </select>
  </div>
</template>

<script>
import axios from 'axios'
import querystring from 'querystring'
export default {
  created () {
    this.getJSON(this.url, this.param)
  },
  props: {
    param: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      value: this.param.val,
      sects: []

    }
  },
  computed: {
    ii3InstituteOption () {
      let option = ''
      for (let i = 0; i < this.sects.length; i++) {
        option += `<option :value='${this.sects[i]}'>${this.sects[i]}</option>`
      }
      return option
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    },
    async getJSON (url, param) {
      let result = querystring.stringify(param)

      let d = await axios.get('http://trick.prj.tw/json/TVarJson_ii3Institute')
      this.sects = d.data.data
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



