<template>
  <div class="trick-side-board">
    <div class="stock-block" v-show="callIsStockPage()">
      <div class="stock-title">
        <p class="stock-code">{{stockCode}}</p>
        <p class="stock-name">{{stockName}}</p>
      </div>
      <div class="stock-auto-complete">
        <SymbolComplete @changeValue="handleSymbolChange"/>
      </div>
    </div>

    <!-- TODO 可以改成 slot -->
    <div class="search-history-block" v-show="callIsStockPage()">
      <p>歷史記錄</p>
      <ul>
        <!-- TODO 限制 5 個不應該寫在這，而是要寫在 share store 那邊 -->
        <li @click="handleStockHistoryClick(item.stockCode)" v-for="(item, index) in searchHistory.slice(0, 5)" :key="index">
          <div>{{item.stockCode}}</div>
          <div>{{item.stockName}}</div>
        </li>
      </ul>
    </div>

    <div class="all-tricks-block">
      <div class="trick-menu" v-show="showTrickMenu" @mouseover="mouseover" @mouseout="mouseout">
        <div class="tags" :key="key" v-for="(tab, key) in trickCates">
          {{key}}
          <div class="tag-group">
            <div class="tag" :key="idx" v-for="(tag, idx) in tab">
              <input type="checkbox" name="tag" :id="'tag-' + tag['desc']" :value="tag"
                v-model="selectedTricks" />
              <label :for="'tag-' + tag['desc']">{{tag["desc"]}}
                <span class="trickMenu-item-checked"></span>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div class="tricks-view" :class="{'showTrickMenu':showTrickMenu}" @mouseover="mouseover" @mouseout="mouseout">
        <span>表格一覽</span>
        <img src="../../assets/img/new/more-light.png" alt="">
      </div>
    </div>

    <div class="trick-selected-block">
      <ul>
        <!-- TODO 改為上層傳下來的 selectedList -->
        <!-- <li v-for="trick in selectedTricks" @click="deleteTrick(trick.desc)">
             <img src="../../assets/img/delete.svg" alt="">
             {{trick.desc}}
             </li> -->
        <li v-for="trickCName in selectedOrderList" @click="deleteTrick(trickCName)">
          <img src="../../assets/img/delete.svg" alt="">
          {{trickCName}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import _debounce from 'lodash.debounce'
import * as R from 'ramda'

import { isStockPage } from '@/router/link'
import { getDefaultTrick } from '@/lib/trickapi'
import { trickapi } from '@/lib/trickapi'
import fetchAC from '@/lib/multiac'
import SymbolComplete from '@/components/SymAc'
import { isValid } from '@/defines/Symbols'
import { parseUrl } from '@/lib/url'

/**
 * @events
 *  updateSelectedTricks
 *  updateSymbol
 */
export default {
  components: {
    SymbolComplete
  },
  props: {
    stockCode: {
      type: String,
      default: ''
    },
    stockName: {
      type: String,
      default: ''
    },
    trickCates: {
      type: Object,
      default: {}
    },
    type: {
      type: String
    },
    selectedOrderList: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      /**
       *
       * @model selectedTricks
       */
      selectedTricks: [],
      showTrickMenu: false,
      _allTricks: {},
      symbol: this.stockCode
    }
  },
  created () {
    const set = new Set();
    // NOTE 用 searchHistory Deep Copy 原始資料，避免資料 reverse() 後被覆寫
    this.searchHistory = JSON.parse(JSON.stringify(this.sharedState.StockCodeSearchHistory))
    // NOTE 用 reverse() 將資料反過來再進行比較，讓最新的在第一個
    this.searchHistory = this.searchHistory.reverse().filter(item => !set.has(item.stockCode) ? set.add(item.stockCode) : false);

    // TODO async created
    /* this.init() */
  },
  watch: {
    trickCates (val) {
      // NOTE 因為 trickCates 是非同步的
      this._allTricks = R.flatten(Object.keys(this.trickCates).map(tab => this.trickCates[tab]))
                         .reduce((acc, it) => { acc[it.ename] = it; return acc; }, {})
      this.init()
    },
    selectedTricks (newVal, oldVal) {
      let diff = R.symmetricDifference(newVal, oldVal)
      this.$emit('updateSelectedTricks', newVal, diff)
    },
    symbol (val, oldVal) {
      if (isValid(val)) { // TODO check valid symbol
        this.$emit('updateSymbol', val)
      }
    }
  },
  methods: {
    async init () {
      // NOTE 這邊是 create default trick 的點
      let queryTrick = this.$route.query.trick
      if (queryTrick === undefined) {
        let defaultTricks = await getDefaultTrick(this.type)
        this.$router.push({ query: { trick: defaultTricks.pages.join(',') }})
        this.selectedTricks = defaultTricks.pages.map(it => this._allTricks[it])
      } else if (queryTrick === '') {
        // ?trick= 時是空字串
        // 可能可以和 queryTrick === undefined 合併
      } else {
        // let tricks = queryTrick.split(',')
        let tricks = parseUrl(queryTrick)
        // todo 要把參數切開
        // URL 中的參數 [ ] 再用  ; 分開
        // trickname[showCol1;showCol2;...]
        // 如果要再設定 value ?
        // trickname[showCol1;showCol2;...](input=val;...)

        // NOTE 目前這邊只有放 trick 名稱
        this.selectedTricks = Object.keys(tricks).map(it => this._allTricks[it])
      }
    },
    mouseover (evt) {
      this.showTrickMenu = true
    },
    mouseout (evt) {
      this.showTrickMenu = false
    },
    deleteTrick (trickCName) {
      this.selectedTricks = R.filter(it => it.desc !== trickCName, this.selectedTricks)
    },
    callIsStockPage () {
      // NOTE 因為 template 沒辦法 call 外部的 function，所以要寫一個 method 代 call
      return isStockPage(this.type)
    },
    handleSymbolChange (val) {
      this.symbol = val
    },
    handleStockHistoryClick(code, name){
      this.$router.push({path: '/' + this.type + '/' + code, query: {trick: this.$route.query.trick}})
     }
  }
}
</script>

<style lang='scss' scoped>
  .trick-side-board {
    > .stock-block {
      > .stock-title {
        margin-top: 25px;
        > p {
          margin: 5px 0;
          font-family: MicrosoftJhengHei;
          font-size: 20px;
          font-weight: bold;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.3;
          letter-spacing: 1.5px;
          text-align: center;
          color: var(--sea-turtle);
        }
      }
    }

    > .search-history-block {
      > p {
        box-shadow: 0 1px 0 0 var(--dark-grey-blue-20);
        padding: 10px 30px;
        background-color: var(--bg-color);

        font-family: MicrosoftJhengHei;
        font-size: 14px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 1.4px;
        color: var(--trickbox-text);
      }
      ul {
        padding: 0px 30px;
        list-style-type: none;
        li {
            cursor: pointer;
            padding: 5px 0;
            color: var(--trickbox-text);
            font-size: 14px;
            letter-spacing: 1.4px;
        }
      }
    }

    > .all-tricks-block {
      > .tricks-view {
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: 0 1px 0 0 var(--dark-grey-blue-20);
        padding: 10px 30px;
        background-color: var(--bg-color);
        font-family: MicrosoftJhengHei;
        font-size: 14px;
        font-weight: 300;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 1.4px;
        color: var(--trickbox-text);
        &.showTrickMenu {
          color: var(--white);
          background-image: linear-gradient(to right, var(--sea-turtle), #19ac8d);
        }
      }
    }

    > .trick-selected-block {
      ul {
        padding: 0;
        li {
          padding: 5px 30px;
          width: 100%;
          list-style-type: none;

          font-family: MicrosoftJhengHei;
          font-size: 14px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: 1.4px;
          color: var(--trickbox-text);

          cursor: pointer;
          > img {
            display: none;
          }
        }

        li:hover {
          background-color: var(--bg-color);
          > img {
            display: inline;
            vertical-align: middle;
          }
        }
      }
    }
  }

  .trick-menu {
    position: absolute;
    left: 20%;
    padding: 12px;
    /* top: 400px; */
    max-width: 750px;
    min-height: 260px;
    max-height: 310px;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow-x: auto;
    border-right: 2px solid var(--sea-turtle);
    border-left: 2px solid var(--sea-turtle);
    border-radius: 4px;
    background-color:var(--light-grey);
    .tags {
      width: 50%;
      color: var(--trickbox-text);
      .tag-group{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        .tag{
          font-size: 14px;
          font-weight: 300;
          input {
            display: none;
          }
          label{
            display: block;
            margin: 3px 10px;
            padding: 4px 0px 4px 12px;
            &:hover{
              cursor: pointer;
              background-color: var(--white);
              box-shadow: 2px 2px 3px -1px var(--trickbox-text);
            }
          }
          input:checked+label{
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: var(--sea-turtle);
            background-color: var(--white);
            box-shadow: 2px 2px 3px -1px var(--trickbox-text);
            .trickMenu-item-checked {
              display: inline-block;
              margin-right: 10px;
              width: 6px;
              height: 6px;
              background-color: var(--sea-turtle);
              border-radius: 50%;
            }
          }
        }
        .tag:nth-child(odd) {
          flex-basis: 51%;
        }
        .tag:nth-child(even){
          flex-basis: 49%;
        }
      }
    }

  }
</style>



