<template>
  <div class='page-selectors'>
    <div @click="pageChange('firstPage')">最前一頁</div>
    <div @click="pageChange('prevPage')">上一頁</div>
    <div @click="pageChange(idx)"
        class="pagination"
         :class="[ idx === currPage ? 'selected-page' : '' ]"
         :key="idx" v-for="(page, idx) in pages">{{ page }}</div>
    <div @click="pageChange('nextPage')">下一頁</div>
    <div @click="pageChange('lastPage')">最後一頁</div>
  </div>
</template>

<script>
 import {range} from 'ramda'

 export default {
     props: {
         pageSelector: {
             type: Object,
             default: {}
         }
     },
     data() {
         return {
             pages: [],
             currPage: null
         }
     },
     watch: {
         pageSelector(newVal) {
             let pow = 10 * (newVal.newsFetchCount - 1);
             this.pages = range(0, 10)
                 .map(it => it + pow)
             this.currPage = newVal.currIdx
         }
     },
     methods: {
         pageChange(args) {
             this.$emit("page-change", args);
         }
     }
 };
</script>

<style lang='scss' scoped>
 .page-selectors {
     display: flex;
     justify-content: center;
     div {
         margin: 0 5px;
         text-decoration: none;
         color: var(--sea-turtle);
         cursor: pointer;
         letter-spacing: 1.8px;
         font-weight: 500;
     }
     .pagination {
         border-radius: 50%;
         background-color: var(--sea-turtle);
         color: var(--white);
         width: 24px;
         display: flex;
         justify-content: center;
         align-items: center;
     }
 }
 .selected-page {
     background-color: blue;
 }
</style>



