<template>
  <div class="table-container">
    <div v-if="isLoading"><Loading></Loading></div>
    <div class="table">
      <!-- NOTE 把表格的 header 另外拉一個 field -->
      <div class='table-field'></div>
      <div class="trick-table" v-html="table['data']"></div>
    </div>
    <div class="right-bar" v-if="isZoomed">
      <div class="right-bar-header">已選表格項目</div>
      <div class="right-bar-items">
        <draggable v-model="headers">
          <div class="right-bar-item" v-for="(item, id) in headers" :key="id">
            <input :id="desc + '-' + id" type="checkbox" :value="item.ename" v-model="selectedItems" />
            <label :for="desc + '-' + id">
              <span class="item-title">{{item.cname}}</span>
              <div class="ckb-item">
                <div class="ckb-item-checked"></div>
              </div>
            </label>
          </div>
        </draggable>
      </div>
    </div>
  </div>
</template>

<script>
// TODO rename TrickTable

import { getTable } from '@/lib/trickapi'
import * as R from 'ramda'
import Loading from '@/components/Loading.vue'
import draggable from 'vuedraggable'

import { getUrlHeaders } from '@/lib/url'

export default {
  components: {
    Loading, draggable
  },
  props: {
    domain: {
      default: ''
    },
    href: {
      default: ''
    },
    input: {
      default: ''
    },
    isZoomed: {
      default: false
    },
    desc: {
      type: String,
      default: undefined
    },
    ename: {
      type: String,
      default: undefined
    }
  },
  created () {
    this.getTable()
  },
  data () {
    return {
      table: '',
      isLoading: true,
      selectedItems: [],
      /**
       * headers 同時用來存排序和選項
       */
      headers: undefined,
      getHeaderFlag: false,

      /**
       * 當 getTable 會改動到一些有 watch 的資料，但正在 init 中，所以那些 event 還不正確
       */
      isInited: true
    }
  },
  watch: {
    selectedItems (val) {
      if (!this.getHeaderFlag) {
        this.getHeaderFlag = true
        return
      }
      // TODO 不顯示無法從 url 中存下來
      //this.$emit('headerChange', this.ename, R.intersection(this.selectedItems, this.headers.map(it => it.ename)))
      if (val.length !== 0)
        this.updateTable()
      else
        this.table = "<table><tr><td>no data</td></tr></table>"
    },
    input () {
      if (this.isInited) return
      this.getTable()
    },
    headers (val) {
      if (this.isInited) return

      // TODO selectedItems 目前不能存
      // this.$emit('headerChange', this.ename, R.intersection(this.selectedItems, val.map(it => it.ename)))
      //this.$emit('headerChange', this.ename, this.selectedItems, val.map(it => it.ename))

      // 因為沒有單獨拿 header 的方式，所以只好重置
      this.selectedItems = this.headers.map(it => it.ename)
      this.$emit('headerChange', this.ename, val.map(it => it.ename))
      this.updateTable(val)
    }
  },
  methods: {
    async updateTable (val = undefined) {
      this.isLoading = true
      if (val === undefined) {
        this.table = await getTable(this.href, this.input, R.intersection(this.selectedItems, this.headers.map(it => it.ename)))
      } else {
        //let shows = R.intersection(this.selectedItems, val.map(it => it.ename))
        let shows = val.map(it => it.ename)
        this.table = await getTable(this.href, this.input, shows)
      }
      this.isLoading = false
    },
    async getTable () {
      this.isLoading = true
      // TODO 如果有 default header 順序，要在這拉
      // 產生 ?show=
      let defaultHeaders = getUrlHeaders(this.$route.query.trick, this.ename)

      this.table = await getTable(this.href, this.input, defaultHeaders)
      this.isLoading = false

      // NOTE 取得 table header 名稱的方法
      let tbl = this.table.data

      // NOTE DOMParser 可能有些 browser 不支援
      let header = []
      let doc = new DOMParser().parseFromString(tbl, 'text/html')
      let tds = doc.getElementsByTagName('tr')[0].getElementsByTagName('td')

      for (let i = 0; i < tds.length; i++) {
        let elm = tds.item(i).getElementsByTagName('span')
        if (elm.length !== 0) {
          header.push({cname: elm[0].innerText, ename: elm[1].innerText})
        }
      }

      // TODO selectedItems 不能記在 url 中
      //if (defaultHeaders === undefined || defaultHeaders.length === 0) {
      header.forEach(it => this.selectedItems.push(it.ename)) // NOTE  default select all
      //} else {
      //this.selectedItems = defaultHeaders
      //}
      this.headers = header

      this.isInited = false
    },
    onUpdate: function (event) {
      // TODO
      let headers = [...this.headers]

      headers.splice(event.newIndex, 0, headers.splice(event.oldIndex, 1)[0])
      // this.headers = [...headers]

      //headers = headers.splice(event.newIndex, 0, headers.splice(event.oldIndex, 1))

      /* this.headers.splice(event.newIndex, 0, this.headers[event.oldIndex])
       * this.headers.splice(event.oldIndex, 1) */
    }
  }
}
</script>

<style lang='scss'>
  .trick-table {
    td {
      border: 0;
      white-space: nowrap;
      font-family: NotoSansCJKtc;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 1.2px;
      text-align: center;
      color: rgba(99, 100, 102, 0.7);
    }

    tr:first-of-type {
      height: 46px;
      border-width: 0px;
      background-color: #F7F9FB;
      color: var(--sea-turtle);
      font-family: NotoSansCJKtc;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 1.2px;
      text-align: center;
    }
    tr:not(:first-of-type) {
      height: 40px;
    }

    table {
      border: 0;
    }

  }
</style>
<style lang='scss' scoped>
  * {
    box-sizing: border-box;
  }
  .table-container {
    position: relative;
    display: flex;
    flex-direction: row;
    .table {
      flex: 1 1 auto;
      margin: 10px;
      display: flex;
      justify-content: center;
    }
    .right-bar {
      flex: 0 1 200px;
      margin: 10px;
      .right-bar-header {
        font-size: 16px;
        text-align: center;
        color: var(--trickbox-text);
        line-height: 40px;
        letter-spacing: 1.2px;
      }
      .right-bar-item {
        &:hover {
          background-color: var(--bg-color);
          & label {
            cursor: pointer;
          }
        }
        input[type="checkbox"] {
          display: none;
          &+label {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .ckb-item {
              display: inline-block;
              border: 1px solid rgba(99, 100, 102, 0.7);
              border-radius: 50%;
              width: 16px;
              height: 16px;
              margin: 10px;
              padding: 1px;
              cursor: pointer;
            }
            .ckb-item-checked {
              display: none;
              width: 100%;
              height: 100%;
              background-color: var(--sea-turtle);
              border-radius: 50%;
            }
            .item-title {
              font-family: MicrosoftJhengHei;
              font-size: 14px;
              font-weight: normal;
              font-style: normal;
              font-stretch: normal;
              line-height: normal;
              letter-spacing: 1.2px;
              color: var(--trickbox-text);

              cursor: pointer;
            }
          }
          &:checked {
            &+label {
              .ckb-item-checked {
                display: block;
              }
              .item-title {
                font-family: MicrosoftJhengHei;
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: 1.2px;
                color: var(--trickbox-text);

                cursor: pointer;
              }
            }
          }
        }

      }
    }
  }
</style>



