<template>
  <div :style="{width: width, height: height}" style="overflow-x:auto;">
    <News
      :result-num='resultNum'
      :result-list='resultList' />
  </div>
</template>

<script>

import Loading from '../components/Loading.vue'
import News from '../components/News.vue'
import newsapi from '../lib/newsapi'
import * as R from 'ramda'

export default {
  components: {
    News
  },
  created () {
    this.fetchNews(this.code)
  },
  data () {
    return {
      resultNum: 0,
      resultList: [],
      sym: this.code,
      isLoading: false,
    }
  },
  props: {
    code: {
      default: ''
    },
    width: {
      default: '100%'
    },
    height: {
      default: '570px'
    },
    showTitle: {
      default: false
    }
  },
  watch: {
    code (val) {
      if (val.length < 4) return
      this.fetchNews(this.code, this.group)
    }
  },
  methods: {
    handleBtn (msg) {
      if (msg === '日線') {
        this.group = 'day'
      } else if (msg === '周線') {
        this.group = 'week'
      } else if (msg === '月線') {
        this.group = 'month'
      } else {
        this.group = 'day'
      }

      this.getData(this.code, this.group)
    },
    async fetchNews (code) {
      this.isLoading = true

      let resultList = []

      const n = await newsapi('news?key=' + code + '&json=1&type=tw')
      n.data.items.forEach(item => resultList.push(item))
      this.resultNum = resultList.length
      this.resultList = resultList

      this.isLoading = false
    }
  }
}
</script>

<style lang='scss'>
  .title {
    font-family: MicrosoftJhengHei;
    // font-family: PingFangTC;
    font-size: 23px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1.8px;
    text-align: left;
  }
  .candlestick {
  }
</style>



