<template>
  <div class="hkReport-side-board">
    <div class="hkReport-block">
      <div class="hkReport-title">
        請輸入欲查詢之<br>報告關鍵字
      </div>
      <div class="hkReport-auto-complete">
        <SymbolComplete
        :placeholder="searchPlaceHolder"
        :acfn="acfn"
        @changeValue="handleSymbolChange"/>
      </div>
    </div>

    <div class="hkReport-select-type">
        <span>{{options.name}}</span>
    </div>

    <div class="hkReport-select-menu" v-for="(option,index) in options.items" :key="index">
      <input type="radio" name="selectTypeRadio" :id="'selectTypeRadio-' + index" :value="option.val"
        v-model="selectType" />
      <label :for="'selectTypeRadio-' + index">{{option.name}}
        <img src="../assets/img/new/check.png" alt="check">
      </label>
    </div>

  </div>
</template>

<script>
/**
 * 這個 component 是從 TrickSideBoard copy 出來，並 修改來的，
 * 因為 TrickSideBoard 太多持定的功能，目前沒空抽成 general use component 所以先分開做
 * TODO 整合 TrickSideBoard 和 SideBoard
 */

import * as R from 'ramda'
import multiac from '@/lib/multiac'
import { isStockPage } from '@/router/link'
import { getDefaultTrick } from '@/lib/trickapi'
import { trickapi } from '@/lib/trickapi'
import fetchAC from '@/lib/multiac'
import SymbolComplete from '@/components/SymAc'
import { isValid } from '@/defines/Symbols'
import { parseUrl } from '@/lib/url'

/**
 * @events
 *  updateSelectedTricks
 *  updateSymbol
 */
export default {
  components: {
    SymbolComplete
  },
  props: {
    options: {
      type: Object,
      default: {}
    },
    searchPlaceHolder: {
      type: String
    }
  },
  data () {
    return {
      /**
       *
       * @model selectedTricks
       */
      selectedTricks: [],
      selectType: 'all'
    }
  },
  created () {
    console.log(this.options)
  },
  watch: {
    trickCates (val) {
      // NOTE 因為 trickCates 是非同步的
      this._allTricks = R.flatten(Object.keys(this.trickCates).map(tab => this.trickCates[tab]))
                         .reduce((acc, it) => { acc[it.ename] = it; return acc; }, {})
      this.init()
    },
    selectedTricks (newVal, oldVal) {
      let diff = R.symmetricDifference(newVal, oldVal)
      this.$emit('updateSelectedTricks', newVal, diff)
    },
    symbol (val, oldVal) {
      if (isValid(val)) { // TODO check valid symbol
        this.$emit('updateSymbol', val)
      }
    },
    selectType (nVal, oVal) {
      this.$emit('option-change', nVal)
    }
  },
  methods: {
    async init () {
      // NOTE 這邊是 create default trick 的點
      let queryTrick = this.$route.query.trick
      if (queryTrick === undefined) {
        let defaultTricks = await getDefaultTrick(this.type)
        this.$router.push({ query: { trick: defaultTricks.pages.join(',') }})
        this.selectedTricks = defaultTricks.pages.map(it => this._allTricks[it])
      } else if (queryTrick === '') {
        // ?trick= 時是空字串
        // 可能可以和 queryTrick === undefined 合併
      } else {
        // let tricks = queryTrick.split(',')
        let tricks = parseUrl(queryTrick)
        // todo 要把參數切開
        // URL 中的參數 [ ] 再用  ; 分開
        // trickname[showCol1;showCol2;...]
        // 如果要再設定 value ?
        // trickname[showCol1;showCol2;...](input=val;...)

        // NOTE 目前這邊只有放 trick 名稱
        this.selectedTricks = Object.keys(tricks).map(it => this._allTricks[it])
      }
    },
    handleSelectType (val) {
      this.selectType = val
    },
    callIsStockPage () {
      // NOTE 因為 template 沒辦法 call 外部的 function，所以要寫一個 method 代 call
      return isStockPage(this.type)
    },
    handleSymbolChange (val) {
      // this.symbol = val
      this.$emit('target-search', val)
    },
    handleStockHistoryClick(code, name){
      this.$router.push({path: '/' + this.type + '/' + code, query: {trick: this.$route.query.trick}})
    },
    acfn (keyword) {
      return multiac('tw', 'hk', keyword)
    }
  }
}
</script>

<style lang='scss' scoped>
  .hkReport-side-board {
    .hkReport-block {
      .hkReport-title {
        margin-top: 25px;
        text-align: center;
        color: var(--trickbox-text);
        p {
          margin: 5px 0;
          font-family: MicrosoftJhengHei;
          font-size: 20px;
          font-weight: bold;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.3;
          letter-spacing: 1.5px;
          text-align: center;
          color: var(--sea-turtle);
        }
      }
      .hkReport-auto-complete{
        margin: 15px 15px 25px 15px;
      }
    }
    .hkReport-select-type {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 30px;
      box-shadow: 0 1px 0 0 var(--dark-grey-blue-20);
      background-color: var(--bg-color);
      font-family: MicrosoftJhengHei;
      font-size: 14px;
      font-weight: 300;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 1.4px;
      color: var(--brownish-grey);
    }

    .hkReport-select-menu{
      display: flex;
      flex-direction: column;
      input {
        display: none;
      }
      label{
        display: block;
        padding: 10px 30px;
        &:hover{
          cursor: pointer;
          background-color: var(--white);
          box-shadow: 2px 2px 3px -1px var(--trickbox-text);
        }
        & img{
          display: none;
        }
      }
      input:checked+label{
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: var(--sea-turtle);
        background-color: var(--white);
        box-shadow: 2px 2px 3px -1px var(--trickbox-text);
        & img {
          display: inline-block;
        }
      }
    }
  }
</style>



