<template>
  <div class="input-container">
    <div v-for="param in inputComponents">
      <!-- <label>{{param['var']}}:</label> -->
      <component :is="inputType(param)" :param="param" @change="updateValue"></component>
    </div>
    <button v-if="updateBtn" @click='update'>送出</button>
  </div>
</template>

<script>

import * as R from 'ramda'

import paramType from './inputs'
import DateInput from './input/Date.vue'
import TextInput from './input/Text.vue'
import ExgInput from './input/Exg.vue'
import RangeInput from './input/Range.vue'
import YQInput from './input/YQ.vue'
import YMInput from './input/YM.vue'
import NumInput from './input/Num.vue'
import QInput from './input/Q.vue'
import SectInput from './input/Sect.vue'
import SymSectInput from './input/SymSect.vue'
import MajorTraderTypeInput from './input/MajorTraderType.vue'
// import FutInput from "./input/Fut.vue";
// import SettleInput from "./input/Settle.vue";
import FutSettleInput from './input/FutSettle.vue'
import FutNameInput from './input/FutName.vue'
import ii3InstituteInput from './input/ii3Institute.vue'
import optTypeInput from './input/optType.vue'
import optExePriceInput from './input/optExePrice.vue'
import symInput from './input/Sym.vue'
import ii3StockNameInput from './input/ii3StockName.vue'
import scoreInput from './input/Score.vue'
import tdrSymInput from './input/tdrSym.vue'

import NoInput from './input/_NoInput.vue'

export default {
  components: {
    DateInput,
    ExgInput,
    TextInput,
    NoInput,
    RangeInput,
    YQInput,
    YMInput,
    NumInput,
    QInput,
    SectInput,
    MajorTraderTypeInput,
    FutSettleInput,
    SymSectInput,
    FutNameInput,
    ii3InstituteInput,
    optTypeInput,
    optExePriceInput,
    symInput,
    ii3StockNameInput,
    scoreInput,
    tdrSymInput
  },
  created () {
    this.update()
  },
  data () {
    return {
      inputs: this.params.reduce((acc, it) => ((acc[it.var] = it.val), acc), {}), // NOTE default 是直接把所有的 param + default value 存下來
      updateBtn: this.params.length !== 0,
      inputComponents: this.updateInputComponents(this.params)
    }
  },
  props: {
    params: {
      default: []
    }
  },
  methods: {
    inputType (param) {
      return paramType(param)
    },
    updateValue (k, v) {
      this.inputs[k] = v
    },
    update () {
      if (this.inputs.length === 0) return
      this.$emit(
        'update',
        Object.keys(this.inputs)
              .map(it => it + '=' + this.inputs[it])
      )
    },
    updateInputComponents (params) {
      // TODO 目前只有一組 futSym + settle，但如果要支援多組 code 要重構
      let grpComponents = ['futSym', 'settle']
      let checkGrpComponents = (param) => R.any(R.equals(param.var))(grpComponents)
      let others = R.reject(checkGrpComponents)(params)
      let grp = R.filter(checkGrpComponents)(params)

      if (grp.length !== 0) { return [...others, { var: 'futSettle', val: grp }] } else { return others }
    }
  }
}
</script>

<style lang='scss'>
  .input-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex: 0 1 auto;

    button {
      width: 40px;
      height: 28px;
      padding: 5px 7px;
      color: var(--white);
      background-image: linear-gradient(to right, var(--sea-turtle), #19ac8d);

      font-family: MicrosoftJhengHei;
      font-size: 12px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 1px;
      text-align: center;
      white-space: nowrap;
      border: none;

    }
    > div {
      display: flex;
      flex: 0 1 auto;
      margin: 10px;
      label, li {
        font-size: 14px;
        color: rgba(99, 100, 102, 0.7);
        line-height: 27px;
        margin-right: 6px;
      }
      input, select {
        font-size: 14px;
        color: rgba(99, 100, 102, 0.7);
        border: solid 1px #ebeced;
        letter-spacing: 1.2px;
        text-align: center;
        padding: 2px 4px;
        border-radius: 6px;
      }

      ul, li {
        width: 95%;
      }
    }
    .inputCmp {
      position: relative;

      .ac-dropdown {
        position: absolute;
        top: 24px;
        left: 0;
        z-index: 3;
        width: 80%;
        max-height: 200px;
        overflow: scroll;
        background-color: var(--white);
        border: 1px solid #eeedeb;
        padding: 5px 10px;
        margin: 0;
        li {
          list-style: none;
          cursor: pointer;
          font-size: 14px;
          margin: 2px 0;
          &:hover {
            background: var(--stock-red);
          }
        }
        .acItem {
          background: var(--stock-red);
        }
      }
    }
  }
</style>



