<template>
  <div>
    <input type="number" v-model="y" @change="changeValue" />
    <select 
      v-model="q" 
      @change="changeValue" 
      v-html="quarterOption">
    </select>
  </div>
</template>

<script>
export default {
    props: {
        param: {
            type: Object,
            default: ''
        }
    },
    data() {
        return {
            y: this.param.val.split('Q')[0],
            q: 'Q' + this.param.val.split('Q')[1]
        }
    },
    computed: {
      quarterOption() {
        let option = ''

        for (let i=1; i<=4; i++){
          option += `<option value=Q${i}>Q${i}</option>`
        }

        return option
      }
    },
    methods: {
        changeValue() {
            this.$emit('change', this.param.var, `${this.y}${this.q}` )
        }
    }
}
</script>

<style lang="scss" scoped>

</style>



