<template>
<div class="box" :class="{'showError':showError}">
  <input @click="show" class="date-input" :size="value.length + 2" v-model="value" @change="updateValue" type='text'/>
  <img class="icon" :src="showIMG" alt="小日曆"> 
</div>
</template>
<script>
import TinyDatePicker from 'tiny-date-picker'
import moduleName from 'tiny-date-picker/tiny-date-picker.css';
import calendar from '../../../assets/img/new/calendar.png'
import calendarError from '../../../assets/img/new/calendar-error.png'

export default {
  created () {
    // console.log(this.param.var)
    // console.log(this.param.val)
    this.$emit('change', this.param.var, this.param.val)
  },
  props: {
    param: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      value: this.param.val,
      showIMG: calendar,
      showError: false,
      dp: {}
    }
  },
  methods: {
    updateValue () {
      this.$emit('change', this.param.var, this.$el['childNodes'][0].value)
      this.value = this.$el['childNodes'][0].value
      if(this.value == ""){
        this.value = "  /    /  "
        this.showIMG = calendarError
        this.showError = true
      }else {
        this.showIMG = calendar
        this.showError = false
      }
    },
    show () {
      TinyDatePicker(this.$el['firstChild'], {
        appendTo: document.querySelector('.box'),
        lang: {
          mode: 'dp-below',
          days: ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
          months: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
          today: '今天',
          clear: '清除',
          close: '關閉'
        },
        mode: 'dp-below',
        format(date) {
          return date.addDays(1).toISOString().substr(0, 10)
        },
      })
    }
  }
}
Date.prototype.addDays = function(days) {
  var date = new Date(this.valueOf())
  date.setDate(date.getDate() + days)
  return date;
}
</script>
<style lang="scss" scoped>
  .box {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding-right: 5px;
    width: 128px;
    height: 26px;
    background-color: var(--white);
    &.showError {
      border: solid 1px var(--dark-coral);
      input {
        color: var(--dark-coral);
      }
      img {
        margin-right: -1px;
      }
    }
    &:hover{
      box-shadow: 0 0 4px 0 var(--dark-grey-blue-40);
    }
    > input {
      width: 95px;
      -moz-width: 50%;
      border-width: 0px;
      border-color: transparent;
      outline: none;
    }
    > img {
      width: 20px;
      height: 20px;
      vertical-align: middle;
    }
  }
</style>



