<template>
  <div class="news">
    <!-- TODO result 要改一下 -->
    <!-- 應該只有在使用 search 才會有 show "找到多少筆" -->
      <!-- <div class="result-txt">{{resultTxt}}</div> -->
        <div class="news-list">
            <div class="news-card" v-for="(news, idx) in resultList.slice(0, newsListCount)" :key="idx">
                <!-- <div class="news-nav">
                    <div class="lang-tag">繁中</div>
                    <div class="news-link"><a :href="news['link']">link</a></div>
                </div> -->
                <div class="title">{{news['title']}}</div>
                <div class="desc" v-html="news['desc']"></div>
                <div class="source-and-time">
                    {{news.rsstitle}}, {{news.date}}
                    <a :href="news['link']" target="_blank">
                        <!-- <img class="img-link" src="../assets/img/new/new-window@3x.png" alt="link" target="_blank" /> -->
                    </a>
                </div>
            </div>
        </div>
        <div class="read-txt">
            <div @click="readInfo('Less')">顯示更少</div>
            <div @click="readInfo('More')" class="readMore">顯示更多</div>
        </div>
        <div class="no-result" v-if="resultNum===0">
            <div class="no-result-txt">
                <p>Uhh ohh!</p>
                <p>我們竟然找不到任何搜尋結果，<br/>來試試別的關鍵字吧！</p>
            </div>
            <div class="no-result-img">
                <img src="../assets/img/new/no-result@3x.png" alt="no-result" />
            </div>
        </div>
    </div>
</template>

<script>

export default {
  beforeMount () {
    // this.getNews()
  },
  created () {
    /* this.$set(this.news, []) */
  },
  data () {
    return {
      // news: [],
      newsListCount: 12,
      resultTxt: '',
    }
  },
  props: {
    resultNum: {
      default: 0
    },
    resultList: {
      default: []
    }
  },
  methods: {
    readInfo(type){
      if(type === 'Less') this.newsListCount = 12
      else this.newsListCount = this.resultList.length
    }
  },
  watch: {
    resultNum () {
      if (this.resultNum === 0) this.resultTxt = ''
      else this.resultTxt = `找到 ${this.resultNum} 個搜尋結果。`
    }
  }
}
</script>

<style lang='scss' scoped>
@import "style-common/scss/media.scss";

* {
  box-sizing: border-box;
}
.panel {
  margin-top: 5px;
  padding: 16px;
  //width: 364px;
  //height: 676px;
  border-radius: 6px;
  background-color: var(--white);
  box-shadow: 0 0 1px 0 rgba(128, 128, 128, 0.5);
}

.news-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.result-txt {
  color: var(--warm-grey);
  font-size: 14px;
  margin: 10px 0;
  padding-left: 25px;
}

.news-list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 0 25px;
}
.news-card {
  width: calc((100% - 20px) / 1);

  @include wild-width() {
    width: calc((100% - 20px) / 3);
  }
  @include large-width() {
    width: calc((100% - 20px) / 3);
  }
  @include normal-width() {
    width: calc((100% - 20px) / 3);
  }
  @include small-width() {
    width: calc((100% - 20px) / 2);
  }
  @include pads-width() {
    width: calc((100% - 20px) / 2);
  }
  @include phone-width() {
    width: calc((100% - 20px) / 2);
  }

  background-color: var(--white);
  padding: 10px 15px;
  border-radius: 4px;
  margin-bottom: 10px;
  height: 215px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  &:not(:nth-child(3n)) {
    margin-right: 10px;
  }
  .title {
    flex: 0 1 auto;
    // margin: 5px 0px;
    color: var(--trickbox-text);
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1.4px;
    overflow: hidden;
  }
  .desc {
    flex: 1 1 100px;
    margin-top: 10px;
    color: var(--trickbox-text);
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 1px;
    word-break: break-word;
    overflow: hidden;
  }
  .source-and-time {
    flex: 0 1 17px;
    margin-top: 10px;
    font-family: MicrosoftJhengHei;
    // font-family: SourceSansPro;
    font-size: 8px;
    letter-spacing: 0.8px;
    color: var(--dark-grey-blue-40);
  }
}
.lang-tag {
  // width: 43.1px;
  position: relative;
  height: 20px;
  border-radius: 3px;
  background-color: var(--white);
  border: solid 1px #f5ba18;
  margin-left: 10px;

  //border-left: 5px solid transparent;

  // font
  //width: 25px;
  //height: 17px;
  font-family: PingFangTC;
  font-size: 12px;
  letter-spacing: 1px;
  text-align: center;
  line-height: 20px;
  color: #f5ba18;
  padding: 0 4px;

  &::before {
    /* width:10px;
                height: 10px; */
    /* height: 20px; */
    content: "";
    display: inline-block;
    position: absolute;
    left: -10px;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: none;
    border-right: 10px solid #f5ba18;
  }
}
.img-link {
  width: 12px;
  height: 12px;
  margin-left: 2px;
}
.read-txt {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  font-family: MicrosoftJhengHei;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 1.2px;
  color: var(--sea-turtle);
  div{
    cursor: pointer;
  }
  .readMore{
    margin: 8px 16px 14px 27px;
  }
}
.no-result {
    display: flex;
    justify-content: center;
}
.no-result-txt {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 30px;
    p:nth-child(1) {
        color: var(--navbar-bg-color);
        letter-spacing: 2.4px;
        font-size: 48px;
        font-weight: 500;
    }
    p:nth-child(2) {
        color: var(--navbar-bg-color);
        letter-spacing: 1.2px;
        font-size: 24px;
        line-height: 2.5;
    }
}
.no-result-img {
    margin: 30px;
    img {
        width: 400px;
    }
}
</style>


