<template>
<input type="number" step="50" min="8000" max="20000" v-model="value">
</template>
<script>
export default {
  props: {
    param: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      value: this.param.val
    }
  },
  methods: {
    updateValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>



