<template>
  <div>
    <input type="number" v-model="value" @change="changeValue" />
  </div>
</template>

<script>
export default {
  props: {
    param: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      value: this.param.val
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



