<template>
  <div>
    <select v-model="value" @change="changeValue">
      <template v-for="sect in sects">
        <option :value="sect">{{sect}}</option>
      </template>
    </select>
  </div>
</template>

<script>
export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    return {
      value: this.param.val,
      sects: [
        'Call', 'Put'
      ]

    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



