<template>
  <div class="inputCmp">
    <div class="fut-settle">
      <input id="futInput"
        @change="changeFutValue"
        @focus="fetchFutAC"
        @keyup="fetchFutAC"
        @blur="inputBlur"
        v-model="futSymVal"
        placeholder='輸入欲查詢的關鍵字' />
    <ul v-show="showFutACDropdown" class="ac-dropdown">
      <li class="ac-candidates"
            @mousedown="chooseAC(match.code)"
            v-for="match in acMatches">
        {{ match.name }}
      </li>
    </ul>
  </div>
  <div  class="fut-settle">
    <select
      v-model="settleVal"
      @change="changeSettleValue" >
        <option v-for="option in settleOptions" v-bind:value="option">
    {{ option }}
  </option>
    </select>
  </div>
  </div>
</template>

<script>
import querystring from 'querystring'
import { getTVarJson } from '@/lib/trickapi'
import fetchFutAC from '@/lib/multiac'
import _debounce from 'lodash.debounce'
import * as R from 'ramda'

export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    let findParam = (type) => R.find(R.propEq('var', type))
    let futSym = findParam('futSym')(this.param.val)
    let settle = findParam('settle')(this.param.val)
    return {
      settleOptions: [],
      // value: this.param.val,
      settleVal: settle.val,
      futSymVal: futSym.val,
      futSymValIsOnFocus: false,
      acMatches: [],
      index: -1
    }
  },
  created () {
    this.fetchSettleAC(this.futSymVal)
    window.addEventListener('keydown', this.ulKeyUpHandler)
  },
  watch: {},
  computed: {
    showFutACDropdown: function () {
      if (this.acMatches.length > 0 && this.futSymValIsOnFocus == true) { return true } else { return false }
    }
  },
  methods: {
    async fetchSettleAC (futSym) {
      let res = await getTVarJson('tfxSettle', ['futSym=' + futSym])
      this.settleOptions = [this.settleVal, ...res.data.data.map(it => it[0])]
    },
    fetchFutAC: _debounce(function (e) {
      this.futSymValIsOnFocus = true
      let keyCode = e.keyCode
      let nums = [38, 40, 13]
      this.futSymVal = this.futSymVal.replace(/[^a-zA-Z0-9]/g, '')
      if (this.futSymVal.trim() === '' || nums.indexOf(keyCode) != -1) return
      let data = fetchFutAC('tw', 'fut', this.futSymVal)

      this.acMatches = []
      this.index = -1
      data.then(rlt => {
        rlt.forEach(element => {
          this.acMatches.push(element)
        })
      })
    }),
    changeFutValue () {
      this.$emit('change', 'futSym', this.futSymVal)
      this.fetchSettleAC(this.futSymVal)
    },
    changeSettleValue () {
      this.$emit('change', 'settle', this.settleVal)
    },
    chooseAC (match) {
      this.futSymVal = match
      this.acMatches = []
      this.$emit('change', 'futSym', this.futSymVal)
      this.fetchSettleAC(this.futSymVal)
    },
    ulKeyUpHandler (e) {
      let keyCode = e.keyCode
      if (keyCode === 13) this.acMatches = []

      let nums = [38, 40] // 處理上下按鈕
      if (this.futSymValIsOnFocus != true || nums.indexOf(keyCode) === -1) return
      keyCode === 38 ? this.index-- : this.index++

      let allLi = document.querySelectorAll('.ac-candidates')
      if (this.index === -1 || this.index === -2) this.index = allLi.length - 1
      if (this.index === allLi.length) this.index = 0

      allLi.forEach(item => { item.classList.remove('acItem') })
      let activeLi = document.querySelectorAll('.ac-candidates')[`${this.index}`]
      activeLi.classList.add('acItem')
      this.futSymVal = this.acMatches[`${this.index}`].code
    },
    inputBlur () {
      this.futSymValIsOnFocus = false
    }
  },
  beforeDestroy () {
    window.removeEventListener('keydown', this.ulKeyUpHandler)
  }
}
</script>

<style lang="scss" scoped>
.fut-settle {
    display: inline-block;
}
</style>



