<template>
  <div class='main-body'>
    <!-- <TargetSearchBar
         :left-options="left_options"
         :placeholder='"輸入欲查詢的新聞關鍵字"'
         @target-search='targetSearch'></TargetSearchBar> -->

    <div class="side-board">
      <TrickSideBoard />
        <!-- :tabList="tabList"
             :type="type"
             :stockCode="sym"
             :stockName="symCName"
             @updateTagMap="updateTagMap"
             @updateStockCode="updateSym" -->
    </div>
    <PageSelector :pageSelector="pageSelector"
      @page-change="updateNews"></PageSelector>

    <News :result-num='resultNum'
      :result-list='newsWindow'></News>
    <Loading v-show="isLoading"></Loading>
    <PageSelector :pageSelector="pageSelector"
      @page-change="updateNews"></PageSelector>

  </div>
</template>

<script>
/* TODO news layout 要移出 */
/* import TargetSearchBar from '../components/TargetSearchBar.vue' */
/* import TrickSideBoard from '@/components/TrickSideBoard' */
import News from '@/components/News.vue'
/* import PageInfo from '@/components/PageInfo.vue' */
import PageSelector from '@/components/PageSelector.vue'
import Loading from '@/components/Loading.vue'
import multiac from '@/lib/multiac'

import newsapi from '@/lib/newsapi'
import * as R from 'ramda'

const searchBarSelection = { name: '語言',
                             parm: 'lang',
                             default: 'tw',
                             items: [{ n: 'ALL', v: 'all' },
                                     { n: '繁中', v: 'tw' }, // { n: 'US中文', v: 'usc' }
                                     { n: '簡中', v: 'cn' },
                                     { n: '英文', v: 'us' },
                                     { n: '影音', v: 'twv'}]} // 還有 cnv
/* { name: '偏好',
 *   parm: 'fav',
 *   default: '',
 *   constraint: (leftVal, rightVal) => {
 *     if (rightVal === 'v' && (leftVal === 'all' || leftVal === 'usc')) {
 *       return true
 *     } else { return false }
 *   },
 *   items: [{ n: '純文字', v: '' }, { n: '純影音', v: 'v' }] } */


const PAGE_SIZE = 25
const PAGE_LIMIT = 10

export default {
  components: {
    News, PageSelector, Loading
  },
  created () {
    let newsCate = searchBarSelection.default
    this.fetchNews(`news?json=1&s=${newsCate}&size=${(PAGE_SIZE * PAGE_LIMIT)}`)
  },
  data () {
    return {
      left_options: searchBarSelection,
      /* right_options: searchBarSelection[1], */ // 左邊不用了
      resultNum: 0,
      resultList: [],
      pageSelector: {},

      pIdx: 0,

      newsWindow: [],
      newsFetchCount: 1,
      prevLink: '',
      nextLink: '',

      isLoading: false
    }
  },
  methods: {
    targetSearch (keyword, leftItem, rightItem) {
      console.log(keyword + ' ' + leftItem)
      // NOTE 用 keyword 搜尋 news
      // rightItem = undefined
      let cate = leftItem
      /* if (leftItem === 'usc' || leftItem === 'all') cate = leftItem
       *   else cate = leftItem + rightItem */

      // let s = `/news?json=1&key=${keyword}&s=${cate}`
      // TODO key 和 s 不能同時存在
      let s = `/news?json=1&key=${keyword}&size=250`
      this.fetchNews(s)
      this.lastSearch = s
    },
    updateNews (args) {
      console.log(args)
      switch (args) {
        case 'firstPage':
          if (this.newsFetchCount > 1) {
            this.newsFetchCount -= 1
            this.fetchNews(this.prevLink + '&size=' + (PAGE_SIZE * PAGE_LIMIT))
          }
          break
        case 'prevPage':
          if (this.pIdx > 0) {
            this.pIdx = this.pIdx - 1
          } else {
            if (this.newsFetchCount <= 1) return
            this.newsFetchCount -= 1
            this.pIdx = PAGE_LIMIT - 1
            this.fetchNews(this.prevLink + '&size=' + (PAGE_SIZE * PAGE_LIMIT))
          }
          this.changePage()
          break
        case 'nextPage':
          let max = PAGE_LIMIT - 1
          if (this.pIdx < max) {
            this.pIdx = this.pIdx + 1
          } else {
            this.newsFetchCount += 1
            this.fetchNews(this.nextLink + '&size=' + (PAGE_SIZE * PAGE_LIMIT))
          }
          this.changePage()
          break
        case 'lastPage':
          this.newsFetchCount += 1
          this.fetchNews(this.nextLink + '&size=' + (PAGE_SIZE * PAGE_LIMIT))
          break
        default:
          this.pIdx = args
          this.changePage()
      }
      this.pageSelector = {
        currIdx: this.pIdx,
        newsFetchCount: this.newsFetchCount
      }
    },
    changePage () {
      this.isLoading = true
      this.newsWindow = R.slice(this.pIdx * PAGE_SIZE, (this.pIdx + 1) * PAGE_SIZE, this.resultList)
      this.isLoading = false
    },
    async fetchNews (param) {
      this.isLoading = true

      let resultList = []
      // NOTE 記下 param （初始的 link）
      this.baseLinkParam = param
      this.pIdx = 0 // NOTE current select page Index
      // this.newsFetchCount = 1 // NOTE init news count

      // TODO 改到一個 NEWS 的 API 下
      const n = await newsapi(param)
      n.data.items.forEach(item => resultList.push(item))
      this.resultNum = resultList.length
      this.resultList = resultList
      this.newsWindow = R.slice(this.pIdx * PAGE_SIZE, (this.pIdx + 1) * PAGE_SIZE, this.resultList)

      this.nextLink = n.data.next
      this.prevLink = n.data.prev
      this.pageSelector = {
        currIdx: this.pIdx,
        newsFetchCount: this.newsFetchCount
      }

      this.isLoading = false
    },
    async handleAC (keyword) {
      let k = keyword.split(/#/)
      // NOTE #後是 code
      let ac_data = await multiac('tw', 'twstk', k[0])
      return ac_data
    }
  }
}
</script>

<style lang='scss' scoped>
  .main-body {
    /* display: flex; */
    /* flex-direction: row; */
    min-height: 70vh;
    height: auto !important;
    max-width: 1280px;
    margin: 0 auto;
  }
  .left-list {
    width: 173px;
    margin-left: 30px;
    margin-right: 20px;
  }
  .middle-list {
    flex-grow: 1;
  }
</style>



