import VueHighcharts from '../_components/Highcharts.vue'
import { CandleStickOpt } from './options.js'

export default {
  components: {
    VueHighcharts
  },
  props: {
    series: Array,
    cfg: Object,
    opt: Object,
    title: String,
    subTitle: String,
    isZoomed: Boolean,
  },
  data () {
    let info = {
      pChg: '',
      cLast: '',
      open: '',
      high: '',
      low: '',
      close: '',
      k: '',
      d: '',
      macd: '',
      dif: '',
      marketValue: ''
    }

    return {
      chartOptions: CandleStickOpt(this.series, this.cfg, this.opt, this, (e) => {
        let chart = this.chart
        let x = e.target.x
        // console.log(this.chart)
        function findY (chartObj, seriesIndex, xValue) {
          let points = chartObj.series[seriesIndex].points
          for (let i = 0; i < points.length; i++) {
            if (points[i].x === xValue) {
              return points[i]
            }
          }
        }
        function findPrevY (chartObj, seriesIndex, xValue) {
          let points = chartObj.series[seriesIndex].points
          for (let i = 1; i < points.length; i++) {
            if (points[i].x === xValue) {
              return points[i - 1]
            }
          }
        }

        let p1 = findY(chart, 0, x) // ohlc
        let p1Prev = findPrevY(chart, 0, x) // ohlc
        let p2 = findY(chart, 1, x) // vol
        /* let p3 = findY(chart, 2, x) // sma */
        let p4 = findY(chart, 3, x) // k
        let p5 = findY(chart, 4, x) // d
        let p6 = findY(chart, 5, x) // macd
        let p7 = findY(chart, 6, x) // dif
        let p8 = findY(chart, 7, x)
        info.open = p1.open
        info.low = p1.low
        info.high = p1.high
        info.close = p1.close
        info.pChg = (p1.close - p1Prev.close).toFixed(2)
        info.cLast = p1.close
        info.k = p4.y
        info.d = p5.y
        info.macd = p6.y
        info.dif = p7.y
      }),
      chart: null,
      info: info,
      sma: true,
      ma: 'day',
      maGroup:[{value:'day',text:'日線'},
                {value:'week',text:'週線'},
                {value:'month',text:'月線'}],
      range: '1m',
      rangeGroup:[{value:'1m',text:'1M'},
                  {value:'3m',text:'3M'},
                  {value:'6m',text:'6M'},
                  {value:'1y',text:'1Y'},
                  {value:'3y',text:'3Y'},
                  {value:'all',text:'ALL'}]
    }
  },
  watch: {
    series () {
      // clean old series
      while (this.chart.series.length > 0) {
        this.chart.series[0].remove(false)
      }

      // add new series
      this.series.forEach(it => {
        this.chart.addSeries(it, false)
      })

      this.chart.redraw()
    },
    title () {
      // this.chart.setTitle({text: this.title})
    },
    subTitle () {
      // this.chart.setTitle(null, {text: this.subTitle + '<span class="dollar">TWD</span>'})
    },
    sma (val) {
      console.log('sma = ' + val)
      this.$emit('btn-clk', 'sma', val)
    },
    ma (val) {
      console.log('ma = ' + val)
      this.$emit('btn-clk', 'ma', val)
    },
    range (val) {
      console.log('range = ' + val)
      this.$emit('btn-clk', 'range', val)
    }
  },
  mounted () {},
  methods: {
    regChart: function (value) {
      // NOTE 把 highcharts 的 instance 記下來，以後要用比較方便。
      this.chart = value
    },
    colorChange(val){
      if(val > 0) return "#d05751"
      else if(val < 0) return "#69c33e"
      else return "var(--navbar-bg-color)"
    },
    shapeChange(val){
      if(val > 0) return 1
      else if(val < 0) return -1
      else return 0
    }
  }
}



