<template>
  <div>
    <input v-model='keyword'
           @keyup='getAC'
           @blur='closeDropdown'
           :placeholder='placeholder' />
    <div class='dropDown'
         v-if='showACDropdown'>
      <li v-for='match in ACMatches'
          :key="match"
          v-on:click='chooseAC(match)'>
        {{match['name']}}{{match['code']}}
      </li>
    </div>

    <button @click='globalSearch'>搜尋</button>
  </div>
</template>

<script>
import _debounce from 'lodash.debounce'

/* import multiac from '../lib/multiac' */

export default {
  data () {
    return {
      placeholder: '輸入欲查詢關鍵字',
      keyword: '',
      ACMatches: [],
      showACDropdown: false
    }
  },
  props: {},
  methods: {
    search () {
      this.$emit('target-search', this.keyword)
    },
    getAC: _debounce(async function () {
      /* let data = await multiac('tw', 'twstk', this.keyword)
       * this.ACMatches = data
       * this.showACDropdown = true */
    }, 200),

    closeDropdown () {
      setTimeout(() => { this.showACDropdown = false }, 200)
    },
    chooseAC (match) {
      this.keyword = `${match['code']}`
      /* console.log(match['name'], match['code']) */
    },
    /* AC () {
     *   let available = this.ACMatches
     *   $('tags').autocomplete({
     *     source: available
     *   })
     * }, */
    globalSearch () {
      window.location.href = `/result.html?${this.keyword}`
    }
  },
  mounted () {}

}
</script>

<style lang='scss' scoped>
  @import '../../../style-common/scss/fonts';
  @import '../../../style-common/scss/media';

  input {
    position: relative;
    bottom: 2px;
    height: 36px;

    background-color: transparent;
    border: 0px;
    outline: 0;

    border-bottom: solid 1px var(--light-white);
    text-align: left;

    width: 160px;
    @include wild-width {
      width: 200px;
    }

    @include responsive-font-size(18px);
    @include above-fonts(var(--light-white));

    ::placeholder,  /* Chrome, Firefox, Opera, Safari 10.1+ */
    :-ms-input-placeholder,
    ::-ms-input-placeholder  /* Microsoft Edge */
    {
      width: 100px;
      height: 25px;
      @include responsive-font-size(18px);
      @include above-fonts(var(--light-white));
    }

  }

  .dropDown {
    background-color: var(--white);
    border: solid 1px #000000;
    border-top: none;
    width: 223px;
    position: absolute;
    top: 38px;

    li {
      list-style-type: none;
      cursor: pointer;
    }
  }

  .layer .text-style-1 {
    letter-spacing: normal;
  }

  button {
    width: 80px;
    height: 30px;
    background-color: var(--white);
    border-radius: 20px;
    border: none;
    text-align: center;
    outline: none;
    @include responsive-font-size(18px);
    @include above-fonts(var(--navbar-bg-color));
  }
</style>



