import * as R from 'ramda'

const def = {
  'TextInput': ['pname'],
  'DateInput': ['day', 'sd', 'ed'],
  'ExgInput': ['exg'],
  'RangeInput': ['roe'],
  'YQInput': ['yq'],
  'YMInput': ['ym'],
  'NumInput': ['y', 'shortTermMonths', 'longTermMonths', 'numSeq'],
  'QInput': ['q'],
  'SectInput': ['symSect'],
  'SymSectInput': ['symSect'],
  'MajorTraderTypeInput': ['futMajTraderType', 'optMajTraderType'],
  'FutSettleInput': ['futSettle'],
  'FutNameInput': ['futName'],
  'ii3InstituteInput': ['ii3Institute'],
  'optTypeInput': ['optType'],
  'optExePriceInput': ['optExePrice'],
  'symInput': ['sym'],
  'ii3StockNameInput': ['ii3StockName'],
  'scoreInput': ['score'],
  'tdrSymInput': ['tdrSym']
}

export default function paramType(param) {
  let alltype = Object.keys(def)


  for (let type of alltype) {
    if (R.contains(param.var, def[type])) {
      return type
    }
  }

  return 'NoInput'
}



