<template>
  <div class="main-body">
    <div class="side-board">
      <TrickSideBoard :trickCates="tabList"
        :type="type"
        :stockCode="sym"
        :stockName="symCName"
        :selectedOrderList="selectedOrderList"
        @updateSelectedTricks="handleTricksChange"
        @updateSymbol="handleSymbolChange" />
    </div>
    <div class="box-container" v-sortable="{onUpdate: handleTrickSortUpdate}">
      <TrickBox
        v-for="(trick, key) in selectedTricks"
        :key="key"
        :sym="sym"
        :trick="trick" />
      <NoResult  v-if="Object.keys(selectedTricks).length == 0" />
    </div>
  </div>
</template>

<script>
import * as R from 'ramda'
import TrickSideBoard from '@/layouts/trick/TrickSideBoard'
import TrickBox from '@/layouts/trick/TrickBox.vue'

import NoResult from '@/components/NoResult.vue'
import { trickapi, getSymCname, getDefaultTrick } from '@/lib/trickapi'
import { isStockPage } from '@/router/link'
import trickCates, { excludeTrickCates } from '@/defines/TrickCates'
import { getStockPageExtraTrick } from '@/defines/ExtraTricks'
import draggable from 'vuedraggable'

import { parseUrl, genUrl } from '@/lib/url'

export default {
  components: {
    TrickSideBoard, TrickBox, NoResult, draggable
  },
  props: {
    defaultSym: {
      type: String,
      default: undefined
    },
    type: {
      type: String,
      default: undefined
    }
  },
  data () {
    let data = {
      cate: trickCates[this.type], /* type 從 router 中定義 */
      selectedTricks: {},
      selectedOrderList: [],
      tabList: {},
      symCName: ''
    }
    let sym = undefined
    if (isStockPage(this.type)) {
      sym = (this.$route.params.sym || this.defaultSym)
      // TODO 在 watch sym 那邊也有更新 symcname 的 code，需整合
      getSymCname(sym,
        cname => {
          this.symCName = cname
          this.sharedState.currSymCName = cname
          this.sharedState.currSym = sym
        })
    }
    return {...data, sym: sym}
  },
  created() {
    this.initTrickCates()
  },
  methods: {
    async initTrickCates () {
      let _trick = await trickapi()
      let isCurrCate = it => it.includes(trickCates[this.type].name)
      let currCate = _trick.getCates().filter(isCurrCate)
      let tabList = currCate.reduce((acc, cate) => {
        let subCate = cate.split('-')[1]
        if (excludeTrickCates(cate)) {
          acc[subCate] = _trick.getCateItems(cate)
        }
        return acc
      }, {})

      // 如果是 type = stock 要多增加 '其它' 這個分類
      if (isStockPage(this.type)) {
        tabList['其它'] = getStockPageExtraTrick()
      }

      this.tabList = tabList
    },

    // Event Handler
    // ===========================
    handleTricksChange (val, diff) {
      // val 是目前有的值
      // diff 是一個 trick data array
      let trick = val.map(it => it.ename).join(',')
      let urlTricks = parseUrl(this.$route.query.trick)

      if (trick !== '' || Object.keys(val) !== 0) {
        // TODO trick change 會有 bug 把原本選好的洗掉
        // 取得舊的 trick url
        // 加入新的 trick url

        /* this.$router.replace({query: {trick: trick}}) */
      }
      diff.forEach(trick => {
        let trickKey = trick.ename // TODO 改英文
        if(this.selectedTricks[trickKey] === undefined) {
          let selectedTricks = this.selectedTricks
          selectedTricks[trickKey] = trick
          this.selectedTricks = selectedTricks

          // NOTE 如果 url 中本來就有該 trick 不要重
          if (urlTricks[trickKey] === undefined) {
            urlTricks[trickKey] = {headers: undefined, inputs: undefined}
          } else {
            // TODO
          }
        } else {
          delete this.selectedTricks[trickKey]
          delete urlTricks[trickKey]
        }
      })

      this.$forceUpdate()

      // 跟者 URL 才不會錯
      this.selectedOrderList = Object.keys(urlTricks).map(ename => this.selectedTricks[ename].desc)
      //

      let newUrl = genUrl(urlTricks)
      this.$router.replace({query: {trick: newUrl}})
    },
    handleSymbolChange (val) {
      this.sym = val
      let trick = this.$route.query.trick
      this.$router.push({path: '/' + this.type + '/' + val, query: {trick: trick}})
    },
    handleTrickSortUpdate (e) {
      /**
       * 這邊直接用 Sortable js 的事件串出來而使用像 draggable 的 api
       * 原本是要 bind data 會影響到流程
       * 以這邊為例：
       * TrickBox 順序改變只要再變動 URL 就好了
       * 不需要改動 selectedTricks 造成不必要的 rerender
       */

      // NOTE this.selectedTricks 可以不用動它
      // 只動 url 就好

      // children 是一個 HTMLCollection
      let nodes = e.to.children
      let urlTricks = parseUrl(this.$route.query.trick)
      let newUrlTricks = {}
      for (let i = 0; i < nodes.length; i++) {
        let node = nodes.item(i)
        // 重排 url
        let trickName = node.getAttribute('trick')
        newUrlTricks[trickName] = urlTricks[trickName]
      }

      this.selectedOrderList = Object.keys(newUrlTricks).map(ename => this.selectedTricks[ename].desc)

      let newUrl = genUrl(newUrlTricks)
      this.$router.replace({query: {trick: newUrl}})
    }
    // ===========================
  },
  watch: {
    sym (val) {
      // TODO 在 data 那邊也有更新 symcname 的 code，需整合
      getSymCname(sym,
        cname => {
          this.symCName = cname
          this.sharedState.currSymCName = cname
          this.sharedState.currSym = sym
        })
    }
  }
}

</script>

<style lang='scss' scoped>

  * {
    box-sizing: border-box;
  }

  .main-body {
    min-height: 70vh;
    height: auto !important;

    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    flex-direction: row;
  }
  .side-board {
    width: 20%;
    /* height: 600px; */
    background-color: var(--white);
    min-height: 800px;
  }

  .box-container {
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: row;
    padding: 0 20px;
  }

  @media (max-width: 768px) {
    .box-container {
      margin-top: 20px;
    }
  }
</style>



