<template>
    <div>
      <input type="number" v-model="y" @change="changeValue" />
      <select 
        v-model="m" 
        @change="changeValue" 
        v-html="monthOption">
      </select>
    </div>
</template>

<script>
export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    return {
      y: this.param.val.split('/')[0],
      m: this.param.val.split('/')[1]
    }
  },
  computed: {
    monthOption () {
      let option = ''

      for (let i = 1; i <= 12; i++) {
        option += `<option value='${i}'>${i}</option>`
      }

      return option
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, `${this.y}/${this.m}`)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



