<template>
  <div>
    <select v-model="value" @change="changeValue">
      <template v-for="sect in sects">
        <option :value="sect">{{sect}}</option>
      </template>
    </select>
  </div>
</template>

<script>
 import { getTVarJson } from '@/lib/trickapi'
 import { flatten } from 'ramda'
 export default {
   props: {
     param: {
       type: Object,
       default: ''
     }
   },
   created () {
     this.getTVarJson()
   },
   data () {
     return {
       value: this.param.val,
       sects: []

     }
   },
   methods: {
     changeValue () {
       this.$emit('change', this.param.var, this.value)
     },
     async getTVarJson () {
       // TODO check 是 fut or opt (雖然結果一樣，但還是要分 (在 backend 是不一樣的))

       let res = await getTVarJson('futMajTraderType')
       this.sects = flatten(res.data.data)
     }
   }
 }
</script>

<style lang="scss" scoped>

</style>



