export default {
  data() {
    return {
      email: "",
      purpose: "",
      text: "",
      noemail: false,
      nopurpose: false,
      notext: false,
      sent: false,
      emailError: false,
      emailErrorMsg: "",
      index : { url: '/index.html', title: '回首頁'}
    };    
  },
  props: {
    domain: {
      type: String,
      default: ""
    }
  },
  methods: {
    updateAccount: function(event) {
      //alert('roe')
      event.target.parentNode.children[0].className = "";
      event.target.parentNode.children[1].className = "";
      event.target.className = "selected";
    },
    firmCombine: function(event) {
      // alert('yield')
      event.target.parentNode.children[0].className = "";
      event.target.parentNode.children[1].className = "";
      event.target.className = "selected";
    },
    check: function(event) {
      var isMail = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
      if (!isMail.test(this.email)) {
        this.noemail = true;
      } else {
        this.noemail = false;
      }

      if (this.text == "") {
        this.notext = true;
      }
      if (this.noemail == false && this.notext == false) {
        this.sent = true;
        console.log("email: " + this.email);
        console.log("purpose: " + this.purpose);
        console.log("text: " + this.text);
      }
      //   this.test = false;
      //   console.log("email:" + this.email);
      //   console.log("purpose:" + this.purpose);
      //   console.log("text:" + this.text);
      //   if (this.email == "") {
      //     this.noemail = true;
      //   } else {
      //     this.noemail = false;
      //   }
      //   if (this.purpose == "") {
      //     this.nopurpose = true;
      //   } else {
      //     this.nopurpose = false;
      //   }
      //   if (this.text == "") {
      //     this.notext = true;
      //   } else {
      //     this.notext = false;
      //   }
      //   if (this.email != "" && this.purpose != "" && this.text != "") {
      //     this.haveSent = true;
      //     this.noemail = false;
      //     this.nopurpose = false;
      //     this.notext = false;
      //     this.$emit("show");
      //     // this.email=''
      //     // this.purpose=''
      //     // this.text=''
      //   }
      //   console.log("sent");
    },
    foo: async function(e) {
      await trickapi();
    },
    confirmed: function() {
      console.log("email: " + this.email);
      console.log("purpose: " + this.purpose);
      console.log("text: " + this.text);
      this.sent=true
    },
    cancel: function() {
        this.sent=false
        this.notext=false
    }
  },
  watch: {
    email: function() {
      var isMail = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
      if (!isMail.test(this.email)) {
        this.emailError = true;
        this.emailErrorMsg = "電子信箱格式錯誤";
      } else {
        this.emailError = false;
        this.emailErrorMsg = "";
      }
    }
  }
};
</script>

