import * as R from 'ramda'

export function commaSeparateNumber (val) {
  while (/(\d+)(\d{3})/.test(val.toString())) {
    val = val.toString().replace(/(\d+)(\d{3})/, '$1' + ',' + '$2')
  }
  return val
}

export function billionCommaSeparateNumber (val) {
  val = (val / 10000000000).toFixed(0)
  while (/(\d+)(\d{3})/.test(val)) {
    val = val.replace(/(\d+)(\d{3})/, '$1' + ',' + '$2')
  }
  return val + 'b'
}

export function padLeft (str, lenght) {
  if (str.length >= lenght) {
    return str
  } else {
    return padLeft('0' + str, lenght)
  }
}

export function getHHMMSS (twQTime) {
  let adjustedDigital = padLeft(String(twQTime), 12)
  let strTime = R.splitAt(-6, adjustedDigital)
  let hhmmss = R.splitEvery(2, strTime[0]).map(it => parseInt(it))
  return { hh: hhmmss[0], mm: hhmmss[1], ss: hhmmss[2] }
}

export default {
  padLeft: padLeft,
  billionCommaSeparateNumber: billionCommaSeparateNumber
}



