<template>
  <div id="app" class="aboveview">
    <Navbar/>
    <router-view
      :key="$route.path" />
    <Footer/>
  </div>
</template>

<script>
import Footer from '@/components/Footer'
import Navbar from '@/components/Navbar'
import trickCates from '@/defines/TrickCates'


import link from '@/router/link'

export default {
  name: 'App',
  components: {
    Footer, Navbar
  },
  methods: {
    updateTitle (newTitle) {
      this.pageTitle = newTitle
    }
  },
  watch: {
    /**
     * Global route change
     * 有一些 state 要在這拉
     * NOTE 只存資料，不處理
     */
    $route (to, from){
      if (from.name === '個股趨勢') {
        // TODO 會有重覆的 code
        if (from.params.sym !== to.params.sym) {
           this.sharedState.StockCodeSearchHistory.push({
             stockCode: this.sharedState.currSym,
             stockName: this.sharedState.currSymCName })
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .aboveview {
    background-color: var(--bg-color);
  }
</style>



