<template>
  <div class="navbar">
    <div class="nav-item nav-title">
      <div class="logo">
        <img src="../assets/img/new/above-logo@3x.png" alt="" width="30" height="23">
      </div>
      <div class="subnav">
        <p>
          豹投資
        </p>
      </div>
    </div>
    <div class="nav-item nav-link"
    :key="idx"
    @mouseover="showSublist=false"
    v-for="(link, idx) in links">
      <!-- <router-link v-if="link.param" :to="{path: link.url, params: { type: link.param }}"> -->
      <router-link v-if="link.param" :to="`${link.url}/${link.param}`">
        <div>{{link.title}}</div>
      </router-link>
      <router-link v-else :to="link.url">
        <div
        :class="{'nav-selectd': `/${params}` === link.url}"
        >
        {{link.title}}
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
import SearchBar from './SearchBar.vue'
import link from '@/router/link.js'
import PageType from '@/enum/PageType.js'

export default {
  components: {
    SearchBar
  },
  computed: {
    params () {
      let path = this.$route['path'].split('/')
      return path[1]
    }
  },
  data () {
    return {
      showSublist: false,
      links: link.genLinkTable(PageType.mainPage),
      sublinks: link.genLinkTable(PageType.subPage)
    }
  }
}
</script>
<style lang="scss" scoped>
.navbar {
  width: 100%;
  height: 54px;
  background-color: var(--navbar-bg-color);
  display: flex;
  align-items: center;
  > .nav-title {
    border-right-style: solid;
    width: 20%;
    min-width: 200px;
  }
  > .nav-link {
    line-height: 54px;
  }
  > .nav-item {
    list-style-type: none;
    color: var(--white);
    > div {
      margin: auto 15px;
      float: left;
      > p {
        line-height: 27px;
        height: auto;
        margin: 0;
      }
    }
    > a {
      display: block;
      margin: auto 0;
      font-family: MicrosoftJhengHei;
      font-size: 16px;
      letter-spacing: 1.6px;
      color: var(--white);
      text-decoration: none;
      > div {
        white-space: nowrap;
        cursor: pointer;
        padding: 0 10px;
        margin: 0 10px;
      }
      > div:hover {
        color: var(--navbar-bg-color);
        background-color: var(--white);
      }
      > .nav-selectd {
        color: #000;
        background-color: var(--white);
      }
    }
  }
}
</style>



