<template>
<div class="broker-box">
  <div class="report-info">
    <span class="title">{{title}}</span>
    <!-- <div :key="key" :report="report" v-for="(report, key) in reports">{{report.date}} {{report.view}} {{report.sym}} {{report.stockName}}</div> -->
    <div :key="key" :report="report"
        class="report-item"
        v-for="(report, key) in reports.slice(pageStart, pageEnd)">
      <div class="report-header">{{report.sym}}<br>{{report.stockName}}</div>
      <div class="report-text">{{title}}</div>
      <div class="report-footer">
        <span class="date">{{report.date}}</span>
        <span @click="clickReport(report.view)" class="read">查看</span>
      </div>
    </div>
  </div>
  <div class="page-control">
    <!-- NOTE 條件成立時(第一頁＆最後一頁)，按鈕變灰色與禁止點選提示 -->
    <p @click="pervPage(5)" :class="{'no-change-page': pageStart == 0}">顯示上5筆</p>
    <p @click="nextPage(5)" :class="{'no-change-page': pageEnd >= reports.length}">顯示下5筆</p>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      title: this.brokerName,
      pageStart: 0,
      pageEnd: 5
    }
  },
  created () {},
  props: {
    reports: {
      type: Array,
      default: () => []
    },
    brokerName: {
      default: ''
    }
  },
  methods: {
    clickReport (path) {
      this.$emit('open-pdf', path)
    },
    pervPage (value) {
      // 判斷是否在第一頁 if => reset pageStart and pageEnd 
      if (this.pageStart - value <= 0) {
        this.pageStart = 0;
        this.pageEnd = value;
      }
      // else => pageStart and pageEnd - 5  資料往前移五筆 
      else {
        this.pageStart = this.pageStart - value;
        this.pageEnd = this.pageEnd - value
      }
    },
    nextPage (value) {
      // 判斷是否還有下一頁 if => pageStart and pageEnd + 5 資料往後移五筆，else 不動作，讓資料維持在最後一頁
      if(this.reports.length > this.pageEnd){
        this.pageStart = this.pageStart + value
        this.pageEnd = this.pageEnd + value
      }
    },
  }
}
</script>
<style lang="scss" scoped>
* {
  box-sizing: border-box;
}
.broker-box {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(100%/3 - 20px);
    justify-content: space-between;
    margin: 10px;
    border-radius: 6px;
    background-color: var(--tab-bg-color);
    box-shadow: 0 0 1px 0 rgba(99, 100, 102, 0.5);
    .report-info{
      .title {
          display: inline-block;
          width: 100%;
          font-size: 18px;
          border-radius: 6px 6px 0 0;
          background-color: #e8f1f5;
          padding: 5px 10px;
          color: var(--navbar-bg-color);
          font-weight: 500;
      }
      .report-item {
        display: flex;
        flex-direction: column;
        margin: 15px;
        padding: 10px;
        font-size: 12px;
        font-weight: 300;
        color: var(--trickbox-text);
        border-bottom: 2px solid var(--sea-turtle);
        border-radius: 4px;
        background-color: var(--white);
          &:hover{
            box-shadow: 0 0 4px 0 var(--dark-grey-blue-40);
            .report-footer {
                .read {
                display: flex;
                cursor: pointer;
              }
            }
          }
          .report-header {
            font-size: 14px;
            font-weight: bold;
            letter-spacing: 1.4px;
            color: var(--trickbox-text);
          }
          .report-text {
            padding-top: 16px;
          }
          .report-footer {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            height: 30px;
            .date {
              color: var(--sea-turtle);
            }
            .read {
              display: none;
              padding: 6px 10px;
              color: var(--white);
              background-image: linear-gradient(to right, var(--sea-turtle), #19ac8d);
            }
          }
      }
    }
    .page-control {
        display: flex;
        justify-content: flex-end;
        padding: 5px;
        .no-change-page{
          color: var(--warm-grey);
          cursor: no-drop;
        }
        p {
          margin: auto 5px;
          font-family: MicrosoftJhengHei;
          font-size: 12px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: 1.2px;
          color: var(--sea-turtle);
          white-space: nowrap;
          cursor: pointer;
        }
    }
}

</style>



