import axios from 'axios'

let domain = process.env.TRICK_URL

export default async function (sym, group, range) {
  //let data = await axios(domain + '/api?sym=' + sym + '&group=' + group + '&limit=50')
  let data = await axios(domain + '/api?sym=' + sym + '&group=' + group + '&range=' + range)
  return data
}



