<template>
  <div :style="{width: width, height: height}" style="overflow-x:auto;">
    <LibCandleStick
      :title='title'
      :subTitle='subTitle'
      :series='series'
      :opt='opt'
      :isZoomed='isZoomed'
      @btn-clk="handleBtn"/>
  </div>

</template>
<script>
import LibCandleStick from 'vue-common/src/components/CandleStick'
import candleapi from '@/lib/candleapi'

/* TODO 改 js-common */
import { billionCommaSeparateNumber } from '../lib/string'

import * as R from 'ramda'

/* function gotColorByOHLC (ohlc, idx) {
 *   // let ohlc = aapl.ohlc[idx]
 *   return ohlc[4] - ohlc[1] > 0 ? '#F69578' : '#69c33e'
 * } */

export default {
  components: {
    LibCandleStick
  },
  created () {
    this.getData(this.code, this.group, this.range)
  },
  data () {
    return {
      series: [],
      title: '',
      subTitle: '',
      opt: { name: '', chartHeight: '600px' },
      sym: this.code,
      group: 'day', // set default
      hasSma: true,  // set default
      range: '1m', //set default
    }
  },
  props: {
    code: {
      default: ''
    },
    width: {
      default: '100%'
    },
    height: {
      default: '570px'
    },
    showTitle: {
      default: false
    },
    isZoomed: {
      default: Boolean
    }
  },
  watch: {
    code (val) {
      if (val.length < 4) return
      this.getData(this.code, this.group, this.range)
    }
  },
  methods: {
    handleBtn (tgt, val) {
      if (tgt === 'ma') {
        this.group = val
        this.getData(this.code, this.group, this.range)
      } else if (tgt === 'sma') {
        this.hasSma = val
        this.getData(this.code, this.group, this.range)
      } else if (tgt === 'range') {
        this.range = val
        this.getData(this.code, this.group, this.range)
      }
    },
    async getData (sym, group, range) {
      let res = await candleapi(sym, group, range)
      let series = []
      series.push({
        type: 'candlestick',
        name: 'OHLC',
        data: res.data.ohlc.map(it => R.take(5, it))
      })

      series.push({
        type: 'column',
        name: 'Vol',
        yAxis: 1,
        data: res.data.ohlc.map(it => [it[0], it[5]]),
        color: '#ebeced'
      })

      series.push({
        type: 'spline',
        name: 'K',
        yAxis: 2,
        data: res.data.kd.map(it => [it[0], it[1]]),
        color: '#3eb3c3'
      })

      series.push({
        type: 'spline',
        name: 'D',
        yAxis: 2,
        data: res.data.kd.map(it => [it[0], it[2]]),
        color: '#c36a3e'

      })

      series.push({
        type: 'spline',
        name: 'MACD',
        yAxis: 3,
        data: res.data.macd.map(it => [it[0], it[2]]),
        color: '#c3b63e'
      })

      series.push({
        type: 'spline',
        name: 'DIF',
        yAxis: 3,
        data: res.data.macd.map(it => [it[0], it[1]]),
        color: '#be63aa'
      })
      series.push({
        type: 'column',
        name: 'OSC',
        yAxis: 3,
        data: res.data.macd.map(it => [it[0], it[1]])
      })
      // SMA
      if (this.hasSma) {
        series.push({
          type: 'spline',
          name: 'SMA5',
          yAxis: 0,
          data: res.data.sma.map(it => [it[0], it[1]]),
          color: 'red'
        })

        series.push({
          type: 'spline',
          name: 'SMA20',
          yAxis: 0,
          data: res.data.sma.map(it => [it[0], it[2]]),
          color: 'green'
        })

        series.push({
          type: 'spline',
          name: 'SMA60',
          yAxis: 0,
          data: res.data.sma.map(it => [it[0], it[3]]),
          color: 'blue'
        })
      }

      this.series = series
      this.title = res.data.info.name + '(' + res.data.info.sym + ')'
      this.subTitle = billionCommaSeparateNumber(Number(res.data.info.marketValue)) // TODO 應該都移到 lib/CandleStick 內
    }
  }
}
</script>

<style lang='scss'>
  .title {
    font-family: PingFangTC;
    font-size: 23px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1.8px;
    text-align: left;
  }
</style>



