<template>
  <div class="footer">
    <!-- TODO 版型不要再分 left-right 了 -->
    <section class="left-section">
      <div class="product">
        <div class="title">產品與服務
          <!-- <i class="fab fa-facebook"></i> -->
        </div>
        <div class="sub">
          <a v-for="(link, idx) in links"
            :href="domain + link.url"
            :key="idx">
            <router-link v-if="link.param" :to="`${link.url}/${link.param}`" class="option">
              <div>{{link.title}}</div>
            </router-link>
            <!-- <div class="option">{{link.title}}</div> -->
          </a>
        </div>
      </div>
      <div class="detail">
        <div class="link">
          <a target="blank" href="https://www.facebook.com/AboveTW/">
            <img src="../assets/img/facebook.svg" alt="facebook連結" />
          </a>
          <a target="blank" href="https://www.youtube.com/channel/UC4ELC5S4KyZ16xQkUb5L_UA">
            <img src="../assets/img/youtube.svg" alt="youtube連結" />
          </a>
        </div>
      </div>
      <div class="detail">
        <div class="title aboutTitle">關於</div>
        <div class="sub">
          <a v-for="(link, idx) in aboutLinks"
            :href="domain + link.url"
            :key="idx">
            <div class="about">{{link.title}}</div>
          </a>
        </div>
      </div>
    </section>
    <!-- TODO copyright -->
  </div>
</template>

<script>
import SearchBar from './SearchBar'
import link from '@/router/link.js'
import PageType from '@/enum/PageType.js'
import * as R from 'ramda'

export default {
  components: {
    SearchBar
  },
  data () {
    return {
      links: R.filter(it => !R.propEq('url', '/')(it))(link.genLinkTable(PageType.mainPage)),
      aboutLinks: link.genLinkTable(PageType.subPage)
    }
  },
  props: {
    domain: {
      type: String,
      default: ''
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
  .footer {

    background-color: var(--navbar-bg-color);
    position: relative;

    // bottom: -webkit-calc(-100% + 40px);
    // bottom: calc(-100% + 40px);

    // width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px;

    .left-section {
      flex: 0 1 50%;
      display: flex;
      justify-content: space-between;
      .product .sub {
        display: flex;
        flex-wrap: wrap;
        a {
          flex: 0 1 50%;
        }
      }
    }
    .icon {
      font-size: 28px;
      color: var(--white)
    }
    .title {
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 1.2px;
      text-align: left;
      color: var(--light-white);
    }
    .option, .about {
      color: var(--white);
      margin-bottom: 5px;
    }
    a {
      text-decoration: none;

    }
  }
  @media (max-width: 768px) {
    .footer {
      flex-direction: column;
      section {
        flex-direction: column;
      }
      .detail {
        .sub {
          display: flex;
          flex-wrap: wrap;
        }
      }
      .searchBar {
        text-align: center;
      }
    }
  }
</style>



