<template>
  <div class="inputRange">
    <input id="processRange" type="range" v-model="value" max="100" step="0.1" @change="changeValue" @input="changeProcess()" :style="{background: '-webkit-linear-gradient(top, var(--trickbox-text), var(--trickbox-text)) 0% 0% / '+ processPoint*100/duration +'% 100% no-repeat'}"/>
    <label class="roeInput">{{value}}%</label>
  </div>
</template>
<script>
export default {
  props: {
    param: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      value: this.param.val,
       processPoint:0,
       duration:'100'
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    },
    changeProcess() {
      let range = document.getElementById('processRange');
      this.processPoint = range.value;
    },
  }
}
</script>
<style lang="scss" scoped>

.inputRange{
  min-width: 205px;
  border: solid 1px #ebeced; 
      &:hover{
      outline: none;
      box-shadow: 0 0 4px 0 var(--dark-grey-blue-40);
    }
  input[type=range] {
    -webkit-appearance: none;
    position: relative; 
    top: -2px; 
    z-index: 10;
    margin-left: 10px;
    padding: 0px;   
    width: 130px;
    height: 6px; 
    border: none;
    border-radius: 0;
    outline : none;     
  }
  // styling for WebKit
  input[type=range]::-webkit-slider-runnable-track {
      -webkit-appearance: none;
      width: 100%;
      height: 6px;
      cursor: pointer;
      background-color: var(--dark-grey-blue-20);
      border-radius: 1.3px;
  }  
  input[type=range]::-webkit-slider-thumb {
      -webkit-appearance: none;
      position: relative; 
      margin-top: -3px; 
      width:12px;
      height:12px;
      border-radius: 50%;
      background: var(--bg-color);
      cursor: pointer;
  }

  // stuff for Firefox
  input[type=range]::-moz-range-track {
      width: 100%;
      height: 6px;
      cursor: pointer;
      background-color: var(--dark-grey-blue-20);
      border-radius: 1.3px;
  }
  input[type=range]::-moz-range-thumb {
      position: relative; 
      margin-top: -3px; 
      width:12px;
      height:12px;
      border-radius: 50%;
      background: var(--bg-color);
      cursor: pointer;
  }
}

</style>


