<template>
  <div>
    <select 
      v-model="value" 
      @change="changeValue" 
      v-html="quarterOption">
    </select>
  </div>
</template>

<script>
export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    return {
      value: this.param.val
    }
  },
  computed: {
    quarterOption () {
      let option = ''

      for (let i = 1; i <= 4; i++) {
        option += `<option value=Q${i}>Q${i}</option>`
      }

      return option
    }
  },
  methods: {
    changeValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



