<template>
    <div class="hk-report">
        <div class="result-txt">{{resultTxt}}</div>
        <div class="report-wrapper" v-for="result in resultList">
            <div class="title">{{result["title"]}}</div>
            <div class="report-list">
                <div class="report" v-for="report in result['list']">
                    <a :href="report[2]">
                        <div class="name">{{report[0]}}</div>
                        <div class="date">{{report[1]}}</div>
                    </a>
                </div>
            </div>    
        </div>
        <div class="no-result" v-if="resultNum===0">
            <div class="no-result-txt">
                <p>Uhh ohh!</p>
                <p>我們竟然找不到任何搜尋結果，<br/>來試試別的關鍵字吧！</p>
            </div>
            <div class="no-result-img">
                <img src="../assets/img/new/no-result@3x.png" alt="no-result" />
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            resultTxt: "",
        }
    },
    props: {
        resultNum: {
            default: 0
        },
        resultList: {
            default: []
        }
    },
    watch: {
        resultNum() {
            if (this.resultNum === 0) this.resultTxt = ""
            else this.resultTxt = `找到 ${this.resultNum} 個搜尋結果。`
        }
    },
    methods: {

    }
}
</script>

<style lang='scss' scoped>
    * {
         box-sizing: border-box;
    }
    .result-txt {
        color: var(--warm-grey);
        font-size: 14px;
        margin: 10px 0;
    }
    .report-wrapper {
        width: 100%;
        margin-bottom: 20px;
        border: 1px solid rgba(128, 128, 128, 0.5);
        border-radius: 6px;
        background-color: var(--white);
        padding: 10px 20px;
        .title {
            color: var(--warm-grey);
            font-size: 18px;
            margin-bottom: 16px;
        }
    }
    .report-list {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .report {
        text-align: center;
        border: solid 1px #fcd35e;
        flex: 0 0 20%;
        padding: 8px;
        a {
            text-decoration: none;
        }
        .name {
            color: #656462;
            font-size: 18px;
        }
        .date {
            color: #fcd35e;
            font-size: 14px;
        }
    }
    .no-result {
        display: flex;
        justify-content: center;
    }
    .no-result-txt {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        margin: 30px;
        p:nth-child(1) {
            color: var(--navbar-bg-color);
            letter-spacing: 2.4px;
            font-size: 48px;
            font-weight: 500;
        }   
        p:nth-child(2) {
            color: var(--navbar-bg-color);
            letter-spacing: 1.2px;
            font-size: 24px;
            line-height: 2.5;
        }
    }
    .no-result-img {
        margin: 30px;
        img {
            width: 400px;
        }
    }
</style>



