<template>
<select v-model="value" @change="updateValue">
  <option value="Tse">上市</option>
  <option value="Otc">上櫃</option>
  <option value="TseOtc">上市櫃全部</option>
  <!-- <option value="Emg">Emg</option>
  <option value="Pub">Pub</option> -->
</select> 
</template>
<script>
export default {
  props: {
    param: {
      type: Object,
      default: ''
    }
  },
  data () {
    return {
      value: this.param.val
    }
  },
  methods: {
    updateValue () {
      this.$emit('change', this.param.var, this.value)
    }
  }
}
</script>



