import axios from 'axios'

const acServer = process.env.MUTI_AC + '/q'

function parseData (str) {
  let s = str.replace('(TW)', '').split('#')
  return {
    name: s[0],
    code: s[1]
  }
}

export default async function (type, cate, keyword) {
  let data = await axios(acServer + '?type=' + type + '&cat=' + cate + '&word=' + keyword)
  return data.data.map(parseData)
}



